import * as _GAME from '../all_modules';
export const tutBom= {
    
    init(){
        _GAME.login.addCallback(data => {
            this.isDoneTutBom = data.isDoneTutBom;
        });
    },
    checkDoneTutBom(){
        if(this.isDoneTutBom) {
            return true;
        }

        return false;
    },
    startTut(){
        if(_GAME.configs.off_power_item)return;
        if(this.showing) return;
        _GAME.utilsFB.log('ev_start_tut_bom',1);
        _GAME.ui.showLayer('layer_tut');
        _GAME.tutBom.startHandAction();
        this.showing = true;
        _GAME.tutBom.showTextHinding();
    },
    startHandAction(){
        const hand = cc.find('Canvas/layer_tut/handTutorial');
        hand.stopAllActions();
        _GAME.utils.setGlobalPositionToNode(hand, cc.v2(cc.find('Canvas/play_area/btn_bomb').x+10,cc.find('Canvas/play_area/btn_bomb').y));
        const nodeContainer  = cc.find('Canvas/play_area/new_item_container');
        _GAME.utils.setGlobalPositionToNode(cc.find('Canvas/layer_tut/text_motivate'),cc.v2(nodeContainer.x,nodeContainer.y+150));
        hand.angle = 120;
        hand.runAction(
            cc.repeatForever(
                cc.sequence(
                    cc.moveTo(1,cc.v2(hand.x,hand.y+70)),
                    cc.moveTo(1,cc.v2(hand.x,hand.y+40))
                )
            )
        );
    },
    endTut() {
        // stop hand fx
        if(_GAME.configs.off_power_item)return;
        _GAME.utilsFB.log('ev_end_tut_bom',1);
        _GAME.tutBom.setTextToMotivate(()=>{
            _GAME.ui.hideLayer('layer_tut');
            // save to fb db that user done tut
            this.showing = false;
            this.isDoneTutBom = true;
            _GAME.data.saveData({ isDoneTutBom: '1' });
        });
    },


    checkHideTutHand() {
        if (this.isDoneTutBom) return;
        const hand = cc.find('Canvas/layer_tut/handTutorial');
        hand.stopAllActions();
        hand.x = 9999;
    },

    checkShowTutHand() {
        if (this.isDoneTutBom) return;
        const hand = cc.find('Canvas/layer_tut/handTutorial');
        _GAME.tut.startHandAction();
    },
    showTextHinding(){
        let nodeLabel = cc.find('Canvas/layer_tut/hinding');
        if(nodeLabel.active == false) nodeLabel.active = true;
        _GAME.utils.setLabelText(nodeLabel,'Try this to clear block');
    },
    hideTextHiding(){
        let nodeLabel = cc.find('Canvas/layer_tut/hinding');
        if(nodeLabel.active == true) nodeLabel.active = false;
    },
    setTextToMotivate(callback){
        let textMotivate = cc.find('Canvas/layer_tut/text_motivate');
        let newNodeMotivate = cc.instantiate(textMotivate);
        _GAME.utils.setLabelText(newNodeMotivate,'GOOD');
        if(newNodeMotivate.active == false)newNodeMotivate.active = true;
        newNodeMotivate.parent = textMotivate.parent;
        newNodeMotivate.runAction(
            cc.sequence(
                cc.spawn(
                    cc.scaleTo(0.5,2),
                    cc.fadeTo(0.5,255)
                ),
                cc.spawn(
                    cc.delayTime(0.6),     
                    cc.sequence(
                        cc.scaleTo(0.15,2.5),
                        cc.scaleTo(0.15,2),
                        cc.scaleTo(0.15,2.5),
                        cc.scaleTo(0.15,2),
                    )
                ),
                cc.spawn(
                    cc.moveTo(0.3,cc.v2(newNodeMotivate.x,newNodeMotivate.y+100)),
                    cc.fadeTo(0.3,0)
                ),
                cc.callFunc(()=>{
                    newNodeMotivate.destroy();
                    if(callback)callback();
                })
            )
        )
    }
}