import * as _GAME from '../all_modules';

export const video = {
    isAvailable: false,
    loadingVideo: false,
    rewardedVideo: null,

    init() {
        if (!window.FBInstant) this.isAvailable = true;
        _GAME.video.preloadVideo(() => {
            _log("PRELOAD VIDEO OK");
            this.isAvailable = true;
        }, (err) => {
            _log("PRELOAD VIDEO ERROR", err);
        });
    },

    preloadVideo(successCallBack, failCallBack) {
        if (!window.FBInstant) {
            if (failCallBack) failCallBack({ code: "OFFLINE" });
            return;
        };

        if (!_GAME.utilsFB.isSupportedAPI("getRewardedVideoAsync")) {
            if (failCallBack) failCallBack({ code: "NOT_SUPPORTED" });
            return;
        }

        if (_GAME.video.loadingVideo) {
            if (failCallBack) failCallBack({ code: "LOADING" });
            return;
        };

        _GAME.video.loadingVideo = true;
        FBInstant.getRewardedVideoAsync(_GAME.configs.videoRewardId)
            .then(rewardedVideo => {
                rewardedVideo.loadAsync().then(() => {
                    _GAME.video.rewardedVideo = rewardedVideo;
                    _GAME.video.loadingVideo = false;
                    if (successCallBack) successCallBack();
                }).catch(err => {
                    _GAME.video.rewardedVideo = null;
                    _GAME.video.loadingVideo = false;
                    if (failCallBack) failCallBack(err);
                });
            }).catch(err => {
                _GAME.video.rewardedVideo = null;
                _GAME.video.loadingVideo = false;
                if (failCallBack) failCallBack(err);
            });
    },

    showVideo(orgSuccessCallBack, orgFailCallBack, videoPos = '') {
        const successCallBack = () => {
            // _GAME.ui.hideNagScreen();
            if (orgSuccessCallBack) orgSuccessCallBack();
        }
        const failCallBack = (err,errorUser) => {
            // _GAME.ui.hideNagScreen();
            if (orgFailCallBack) orgFailCallBack(err,errorUser);
        };
        // _GAME.ui.showLoading();
        if (!window.FBInstant) return successCallBack();

        if (_GAME.video.rewardedVideo) {
            _GAME.utilsFB.log(`ev_video_click_show`);
            _GAME.video.rewardedVideo.showAsync().then(() => {
                _GAME.utilsFB.log("ev_watch_video_success", 1, {});

                _GAME.utilsFB.log(`Ads-Total-Video`, 1, { videoPos });
                _GAME.utilsFB.log(`Ads-Total`);

                successCallBack();
                _GAME.video.preloadVideo();
            }).catch(err => {
                console.log("VAO CASE NAY CO !!!",err.code,' !!! ' ,err.message );
                if (_GAME.video.isErrRewardNotCompleted(err)) {
                    _GAME.utilsFB.log(`ev_video_err_reward_not_completed`);
                } else {
                    _GAME.utilsFB.log(`ev_video_err_other`);
                }
                failCallBack(err,_GAME.video.isErrRewardNotCompleted(err));
                _GAME.video.preloadVideo();
            });
        } else if (_GAME.video.loadingVideo) {
            _GAME.utils.setTimeout(() => _GAME.video.showVideo(successCallBack, failCallBack, videoPos), 200);
        } else {
            failCallBack({ code: "VIDEO_IS_NULL" });
        }
    },

    isErrRewardNotCompleted(err) {
        return err.code == 'USER_INPUT';
    },

}