import * as _GAME from '../all_modules';

export const audio = {
    audioList: {},
    playingIdList: {},
    isSoundOff: true,

    init() {
        _GAME.login.addLoginDataField("isSoundOff");
        _GAME.login.addCallback(data => {
            this.isSoundOff = data.isSoundOff;
            this.renderBtnSound();
            this.loadAudios();
        });

        // bind event for btn sound on/off
        const btnSoundOff = cc.find('Canvas/layer_pause/btn_sound_off');
        _GAME.utils.addButtonComp(btnSoundOff, () => {
            this.isSoundOff = false;
            this.playMusic();
            _GAME.ui.showNagScreen();
            _GAME.data.saveData({ isSoundOff: false }, () => {
                this.renderBtnSound();
                _GAME.ui.hideNagScreen();
            });
        });

        const btnSoundOn = cc.find('Canvas/layer_pause/btn_sound_on');
        _GAME.utils.addButtonComp(btnSoundOn, () => {
            this.isSoundOff = true;
            this.stopMusic();
            _GAME.ui.showNagScreen();
            _GAME.data.saveData({ isSoundOff: true }, () => {
                this.renderBtnSound();
                _GAME.ui.hideNagScreen();
            });
        });
    },

    renderBtnSound(isSoundOff) {
        cc.find('Canvas/layer_pause/btn_sound_off').active = this.isSoundOff;
        cc.find('Canvas/layer_pause/btn_sound_on').active = !this.isSoundOff;
    },

    loadAudios() {
        cc.resources.loadDir('audios', cc.AudioClip, (err, res) => {
            if (err) return _log(err);
            for (let clip of res) this.audioList[clip.name] = clip;
            // _log(`audio.js >> all audio loaded !`);
            this.playMusic();
        });
    },

    playSound(name, volume = 1) {
        if (this.isSoundOff) return;
        this.playingIdList[name] = cc.audioEngine.play(this.audioList[name], false, volume);
    },

    stopSound(name) {
        if (this.playingIdList[name]) cc.audioEngine.stopEffect(this.playingIdList[name]);
    },

    playMusic() {
        if (this.isSoundOff) return;
        if (cc.audioEngine.isMusicPlaying()) return;
        try {
            this.playingIdList["back_ground_music"] = cc.audioEngine.playMusic(this.audioList["back_ground_music"], true, 1);
        } catch (e) { _log(`playMusic err `, e); }
    },

    stopMusic() {
        if (cc.audioEngine.isMusicPlaying()) cc.audioEngine.stopMusic();
    },

    playSoundClickButton() {
        if (this.isSoundOff) return;
        this.playSound("click", 0.2);
    },
    updateUiSoundButton(){
        let btn_sound_on = cc.find('Canvas/layer_pause/btn_sound_on');
        let btn_sound_off = cc.find('Canvas/layer_pause/btn_sound_off');
    }
}