import * as _GAME from '../all_modules';

export const uiChallenge={
    challengedFriendArr: [],
    countCancelChallenge: {
        key: '',
        value: 0
    },
    init() {
        _GAME.login.addLoginDataField("countSaving");
        _GAME.login.addCallback(data => {
            if (data) {
                let nDate = new Date();
                if (!data.countSaving) {
                    let [date, month, year] = nDate.toLocaleDateString().split("/");
                    let key = `${date}/${month}/${year}`;
                    _GAME.uiChallenge.countCancelChallenge.key = key;
                    _GAME.uiChallenge.countCancelChallenge.value = 0;
                    _GAME.data.saveData({ 'countSaving': _GAME.uiChallenge.countCancelChallenge });
                } else {
                    let dataCountCancelChallenge = data.countSaving;
                    if (!dataCountCancelChallenge) {
                        let [date, month, year] = nDate.toLocaleDateString().split("/");
                        let key = `${date}/${month}/${year}`;
                        _GAME.uiChallenge.countCancelChallenge.key = key;
                        _GAME.uiChallenge.countCancelChallenge.value = 0;
                        _GAME.data.saveData({ 'countSaving': _GAME.uiChallenge.countCancelChallenge });
                    } else {
                        let key = dataCountCancelChallenge.key;
                        // console.log('loginData  Key ', key);
                        let [dataDate, dataMonth, dataYear] = key.split('/');
                        let [date, month, year] = nDate.toLocaleDateString().split("/")
                        if (dataYear == year) {
                            if (dataMonth == month) {
                                if (dataDate == date) {
                                    _GAME.uiChallenge.countCancelChallenge.key = key;
                                    _GAME.uiChallenge.countCancelChallenge.value = dataCountCancelChallenge.value;
                                } else {
                                    _GAME.uiChallenge.countCancelChallenge.key = `${date}/${month}/${year}`;
                                    _GAME.uiChallenge.countCancelChallenge.value = 0;
                                }
                            } else {
                                _GAME.uiChallenge.countCancelChallenge.key = `${date}/${month}/${year}`;
                                _GAME.uiChallenge.countCancelChallenge.value = 0;
                            }
                        } else {
                            _GAME.uiChallenge.countCancelChallenge.key = `${date}/${month}/${year}`;
                            _GAME.uiChallenge.countCancelChallenge.value = 0;
                        }
                        // console.log('loginData   ', _GAME.uiChallenge.countCancelChallenge);
                    }
                }
            }
        });
    },
    setDataChallenge(callback) {
        const you = cc.find('Canvas/layer_challenge/content/bg_left');
        const friend = cc.find('Canvas/layer_challenge/content/bg_right');
        const myScore = cc.find('Canvas/layer_challenge/content/lb_score');
        _GAME.utils.setLabelText(myScore, _GAME.user.score+'!', true);
        _GAME.uiChallenge.setDirectorData(you, _GAME.user,true);
        this.selectedFriend = _GAME.friend.getRandomFriendInList();
        if (this.selectedFriend) {
            _GAME.uiChallenge.setDirectorData(friend, this.selectedFriend, false);
        } else {
            // _GAME.uiChallenge.closeChallenge(() => _GAME.uiGameOver.openPanelGameOver);
        }
        if (callback) callback();
    },
    setDirectorData(node, data, isCrown) {
        if (node) {
            if (data) {
                const avata = node.getChildByName('avata');
                const score = node.getChildByName('lb_point');
                const name = node.getChildByName('lb_name');
                const crown = node.getChildByName('crown');
                console.log('data.name   ',data.name, '===> data.score  ' ,data.score);
                _GAME.utils.setLabelTextPrice(score, data.score);
                _GAME.utils.setLabelText(name, data.name);
                crown.active = isCrown;
                cc.assetManager.loadRemote(data.photo, (err, texture) => {
                    if (!err) {
                        let ava = new cc.SpriteFrame(texture);
                        avata.getComponent(cc.Sprite).spriteFrame = ava;
                        avata.width = 198;
                        avata.height = 200;
                    }
                });
            }
        }
    },
    onClickCancel() {
        _GAME.uiChallenge.closeChallenge(() => {;
            _GAME.uiChallenge.saveCountCancel();
            _GAME.uiGameOver.openPanelGameOver(true);
            // _GAME.logic.replayNewGame();
            // _GAME.ui.resetListTouch();
            // _GAME.mapControl.replayNewGame();
        });
    },
    saveCountCancel() {
        let nDate = new Date();
        let [date, month, year] = nDate.toLocaleDateString().split("/");
        let key = `${date}/${month}/${year}`
        if (_GAME.uiChallenge.countCancelChallenge) {
            if (key == _GAME.uiChallenge.countCancelChallenge.key) {
                _GAME.uiChallenge.countCancelChallenge.value++;
                _GAME.data.saveData({ 'countSaving': _GAME.uiChallenge.countCancelChallenge });
            }
        }
    },
    resetCountCancel() {
        let nDate = new Date();
        let [date, month, year] = nDate.toLocaleDateString().split("/");
        let key = `${date}/${month}/${year}`
        if (_GAME.uiChallenge.countCancelChallenge) {
            if (key == _GAME.uiChallenge.countCancelChallenge.key) {
                _GAME.uiChallenge.countCancelChallenge.value = 0;
                _GAME.data.saveData({ 'countSaving': _GAME.uiChallenge.countCancelChallenge });
            }
        }
    },
    runActionShowItem() {
        const mainContent = cc.find('Canvas/Chalenge_scene/content/main_content');
        // const promoContent = cc.find('Canvas/Chalenge_scene/content/promo_content');
        // if (_GAME.config.promoEndGame) {
        //     if (window.canPromo) {
        //         promoContent.active = true;
        //     }

        // } else {
        //     promoContent.active = false;
        // }
        // mainContent.position = _GAME.config.challengePosition[_GAME.config.promoEndGame];

        const textGreate = cc.find('Canvas/layer_challenge/content/header/lb_top');
        _GAME.fx.showTextGreate(textGreate);

        const start1 = cc.find('Canvas/layer_challenge/content/header/star_left');
        const start2 = cc.find('Canvas/layer_challenge/content/header/star_center');
        const start3 = cc.find('Canvas/layer_challenge/content/header/star_right');
        _GAME.fx.animStar([start1, start2, start3]);

        const youAvata = cc.find('Canvas/layer_challenge/content/bg_left/avata');
        const friendAvata = cc.find('Canvas/layer_challenge/content/bg_right/avata');
        _GAME.fx.effectForInfo(youAvata, -100);
        _GAME.fx.effectForInfo(friendAvata, 100);

        const btnPlay = cc.find('Canvas/layer_challenge/content/btn_c_play');
        const btnPlayAnother = cc.find('Canvas/layer_challenge/content/btn_c_play_another');
        const btnCancel = cc.find('Canvas/layer_challenge/content/btn_c_cancel');
        _GAME.fx.effectForButton(btnPlay, 1);
        _GAME.fx.effectForButton(btnPlayAnother, 1.5);
        _GAME.fx.effectForButton(btnCancel, 2.75);
    }
}