import * as _GAME from '../all_modules';
cc.Class({
    extends: cc.Component,

    properties: {},
    onLoad(){
        this.nodeProxy = _GAME.localize.getProxyNode();
        this.nodeProxy.on('TRANFORM_TEXT',this.changeText,this);
    },
    onDestroy(){
        this.nodeProxy.off('TRANFORM_TEXT',this.changeText,this);
    },
    onEnable(){
        this.nodeProxy.on('TRANFORM_TEXT',this.changeText,this);
        this.changeText();
    },
    // onDisable(){
    //     const nodeProxy = _GAME.localize.getProxyNode();
    //     nodeProxy.off('TRANFORM_TEXT',this.changeText,this);
    // },
    changeText(){
        // console.log(this.node.name ,' ==> ',_GAME.localize.pointerLang,'/',_GAME.localize.currentLanguage);
        if(_GAME.localize.pointerLang != 0 && _GAME.localize.currentLanguage){
            if(this.node.getComponent(cc.Label)){
                if(this.node.getComponent(cc.Label).overflow != 2)this.node.getComponent(cc.Label).overflow = 2;
                let cache = this.node.getComponent(cc.Label)._cache;
                if(!cache){
                    this.node.getComponent(cc.Label)._cache =  this.node.getComponent(cc.Label).string;
                    cache = this.node.getComponent(cc.Label)._cache;
                }
                // console.log('Cache  ',cache);
                // console.log('Cache   ',cache);
                let valueCompare = cache.replaceAll(' ','').toUpperCase();
                // console.log('valueCompare   ',valueCompare);
                _GAME.utils.setLabelText(this.node,_GAME.localize.convertPosition(valueCompare));
            }
        }
       
    }
});
