
import * as _GAME from './all_modules';

cc.Class({
    extends: cc.Component,
    // editor: { executeInEditMode: true },

    onLoad() {
        
        window._GAME = _GAME;

        // call init for all modules that has init function
        for (let moduleName in _GAME) {
            if (_GAME[moduleName].init) {
                _GAME[moduleName].init();
               
            }
        }
        _GAME.utilsFB.log('ev_game_load_time', performance.now());
        _GAME.cocosLoadTime = (performance.now() / 1000).toFixed(2);
        _GAME.utils.setLabelText(cc.find('Canvas/layer_pause/label_load_time'), _GAME.cocosLoadTime);

        // subscribe bot
        _GAME.utils.setTimeout(() => {
            _GAME.social.askBotSubscribe(() => _GAME.social.askShortcut());
        }, 1000);

        // remove loading bg: htmlLoadingBackground (html div tag)
        const loadingBg = document.getElementById('htmlLoadingBackground');
        if (loadingBg) loadingBg.style.display = 'none';


    },
  
});