import * as _GAME from '../all_modules';
export const user = {
    id: 0,
    name: 0,
    score: 0,
    bestScore: 0,

    init() {
        if (window.FBInstant) {
            _GAME.user.id = FBInstant.player.getID();
            _GAME.user.name = FBInstant.player.getName();
            _GAME.user.photo = FBInstant.player.getPhoto();
            window.canPromo = (FBInstant.getPlatform() == 'IOS') ? false : false;
        } else {
            _GAME.user.id = 'xxxxaa111';
            _GAME.user.name = "Tkgiet";
            _GAME.user.photo = '';//"https://platform-lookaside.fbsbx.com/platform/instantgames/profile_pic.jpg?igpid=3945443978807076&height=256&width=256&ext=1587452957&hash=AeS0fb6W2zGFcKOE";
        }
    },

    initNewUser(data) {
        Object.assign(data, {
            LoginTime: 0,
            PlayerExp: 0,
            PlayerLevel: 0,
        });

        _GAME.data.saveData(data);
    },

}