import * as _GAME from './all_modules';

export const configs = {
    //  =================================================
    // leaderboard
    off_inter_ads: false,
    off_power_item:false,
    leaderboardName: 'score_global',
    leaderboardPlayerCount: 15,
    isShowPromoGames: (cc.sys.os == cc.sys.OS_IOS) ? false : true,
    listIdPromoGame: ['1290260324506853', '227806484682918', '617940745660759'],
    totalTurnAds: 3,
    canShowAndLoadInter:false,
    //  =================================================
    // --- interstitial ads
    interAdId: '1039089903197791_1060209887752459',
    interAdRatio: {
        gameOverReplay: 1,
    },

    videoRewardId: '1039089903197791_1045666869206761',
    videoContinueTime:1, // times to watch viedeo continue each game

    //challenge 
    maxCountCancelChallenge : 3,
    showInterAdAfterEveryReplayTime:3,
    // ===== biasType & blockNumbers config from stepCount to generate new items 
    biasType: {
        1: ['right_bias', 'left_bias', 'single_block'],
        2: ['right_bias', 'horizontal', 'single_block'],
        3: ['right_bias', 'left_bias', 'horizontal'],
        4: ['right_bias', 'left_bias', 'horizontal', 'single_block'],
    },
    stepCount2BiasTypeArr(stepCount) {
        let lastStep = 0;
        for (let i in this.biasType) {
            if (i > stepCount) return this.biasType[lastStep];
            if (i <= stepCount) lastStep = i;
        }
        return this.biasType[lastStep];
    },

    blockNumber: {
        1: [1, 2, 3, 4, 5, 6],
    },
        // blockNumber: {
        //     1: [6],
        // },
    stepCount2BlockNumerArr(stepCount) {
        let lastStep = 0;
        for (let i in this.blockNumber) {
            if (i > stepCount) return this.blockNumber[lastStep];
            if (i <= stepCount) lastStep = i;
        }
        return this.blockNumber[lastStep];
    },


    // ===== scoring point calculation

    // single point of single block
    singleBlockPoint: {
        1: 2,
        2: 4,
        3: 8,
        4: 16,
        5: 32,
        6: 64,
        7: 128,
    },

    // multiple block scoring factor
    scoreFactor: {
        3: 3,
        4: 3.5,
        5: 5,
        6: 6.5,
        7: 8,
    },

    calculateScore(blockNumber, blockCount) {
        return parseInt(_GAME.configs.singleBlockPoint[blockNumber] * (_GAME.configs.scoreFactor[blockCount] || 1));
    },


}