                if (!_GAME.tut.isTutDone) return;
                if (_GAME.tutTriple.showing) return;
                if (_GAME.tutBom.showing) return;
                if (_GAME.tutRolated.showing) return;
                if (_GAME.tutRelocated.showing) return;
