    // ====== supportive
    disableButton(but) {
        if (!but.getComponent(cc.Button)) _log('KHoong ton tai button component');
        if (but.getComponent(cc.Button)) but.getComponent(cc.Button).enabled = false;
        but.opacity = parseInt(0.3 * 255);
    },

    enableButton(but) {
        if (but.getComponent(cc.Button)) but.getComponent(cc.Button).enabled = true;
        but.opacity = 255;
    },


    showCompliment(text) {
        const container = cc.find('Canvas/play_area/labels/compliment');
        const label = container.getChildByName('label');
        _GAME.utils.setLabelText(label, text);
        container.stopAllActions();
        container.scale = 0;
        container.runAction(cc.sequence(
            cc.scaleTo(0.2, 1.15).easing(cc.easeOut(3)),
            cc.scaleTo(0.1, 1).easing(cc.easeIn(1)),
            cc.delayTime(2),
            cc.scaleTo(0.1, 1.15).easing(cc.easeOut(1)),
            cc.scaleTo(0.2, 0).easing(cc.easeIn(3)),
        ));
    },
    updateScore(score) {
        const playAreaLabelScore = cc.find('Canvas/play_area/labels/label_score');
        const userScoreResult = cc.find('Canvas/layer_result/score');
        playAreaLabelScore.runAction(cc.sequence(
            cc.scaleTo(0.1, 1.5),
            cc.callFunc(() => _GAME.utils.setLabelText(playAreaLabelScore, _GAME.utils.localizeNumber(99999) + '')),
            cc.scaleTo(0.17, 1),
        ));
        _GAME.utils.setLabelText(userScoreResult, _GAME.utils.localizeNumber(score) + '')
        _GAME.utils.setLabelText(
            cc.find('Canvas/layer_leaderboard/label_your_score'),
            _GAME.utils.localizeNumber(score) + '',
        );
    },
    updateHighScore(){
        const playAreaLabelBestScore  = cc.find('Canvas/play_area/labels/label_best_score');
        const userBestScoreResultt = cc.find('Canvas/layer_result/best_score');
        playAreaLabelBestScore.runAction(cc.sequence(
            cc.scaleTo(0.1, 1.5),
            cc.callFunc(() => _GAME.utils.setLabelText(playAreaLabelBestScore, _GAME.utils.localizeNumber(_GAME.user.bestScore) + '')),
            cc.scaleTo(0.17, 1),
        ));
        _GAME.utils.setLabelText(userBestScoreResultt, _GAME.utils.localizeNumber(_GAME.user.bestScore)+ '');
    },

    


