import * as _GAME from '../all_modules';
const BlockAsset = require('blockAsset');
export const mapVisual = {
    init() {
        const sampleBlock = cc.find('Canvas/sample_block');

        // add sample blocks to all cells of map
        cc.find(`Canvas/play_area/map`).children.map(cell => {
            const newBlock = _GAME.utils.copyNodeTo(sampleBlock, cell);
            newBlock.active = false;
            cell.cellNumber = cell.name.replace('Image_HexBlock_', '');
        });

        // add sample blocks to all newItem blocks
        cc.find('Canvas/play_area/new_item_container/new_item/item_types').children.map(typeNode => {
            typeNode.children.map(blockContainer => {
                const newBlock = _GAME.utils.copyNodeTo(sampleBlock, blockContainer);
                newBlock.active = false;
            })
        })
    },

    resetSingleCell(cellNumber) {
        const block = cc.find(`Canvas/play_area/map/Image_HexBlock_${cellNumber}/sample_block`);
        if (!block) return;
        block.active = false;
        block.x = block.y = 0;
        return block;
    },

    resetMap() {
        cc.find(`Canvas/play_area/map`).children.map(cell => this.resetSingleCell(cell.name.replace('Image_HexBlock_', '')));
    },

    decorBlock(blockNode, blockNumber) {
        blockNode.getChildByName('x').active = false;
        blockNode.getChildByName('Image_HexBlock').getComponent(cc.Sprite).spriteFrame = blockNode.getChildByName('Image_HexBlock').getComponent(BlockAsset).assetBlock[parseInt(blockNumber)-1];
        if(blockNumber != 7){
            _GAME.utils.setLabelText(blockNode.getChildByName('label'),  blockNumber);
        }else{
            // blockNode.getChildByName('block7stroke').active = true;
            blockNode.getChildByName('x').active = true;
            _GAME.utils.setLabelText(blockNode.getChildByName('label'),  '');
        }
        
        blockNode.active = true;
    },

    renderSingleCell(cellNumber, blockNumber) {
        if (!blockNumber) return;
        const block = this.resetSingleCell(cellNumber);
        this.decorBlock(block, blockNumber);
    },

    renderMap(mapState) {
        this.resetMap();
        for (let cellNumber in mapState) this.renderSingleCell(cellNumber, mapState[cellNumber]);
    },
}


