import * as _GAME from '../all_modules';

export const tut = {

    // map state for tut
    tutMapState: {
        9: 1,
        11: 1
    },
    targetCell: 10,

    init() {
        _GAME.login.addCallback(data => {
            this.isTutDone = data.isTutDone;
            if (!data.isTutDone) this.startTut();
        });
    },

    startTut() {
        // start handFx
        _GAME.ui.showLayer('layer_tut');
        _GAME.tut.startHandAction();
    },

    startHandAction() {
        const hand = cc.find('Canvas/layer_tut/handTutorial');
        hand.stopAllActions();
        _GAME.utils.setGlobalPositionToNode(hand, cc.find('Canvas/play_area/new_item_container'));

        const targetCell = cc.find(`Canvas/play_area/map/Image_HexBlock_${this.targetCell}/bg`);
        const targetCellGlobalPos = _GAME.utils.getGlobalPosition(targetCell);
        const targetCellPosDiff = _GAME.utils.global2LocalPosDiff(hand, targetCellGlobalPos.x, targetCellGlobalPos.y);
        targetCellPosDiff.y - 20;

        const animTime = 1;
        hand.runAction(cc.repeatForever(
            cc.sequence(
                cc.moveBy(animTime, targetCellPosDiff).easing(cc.easeOut(1)),
                cc.delayTime(0.8),
                cc.moveBy(0, targetCellPosDiff.mul(-1)),
                cc.moveBy(0.3, cc.v2(0, -10)),
                cc.moveBy(0.15, cc.v2(0, 10)),
            )
        ));

        // highlight target cell
        if (targetCell.orgOpacity) targetCell.opacity = targetCell.orgOpacity;
        targetCell.orgOpacity = targetCell.opacity;
        targetCell.opacity *= 3;
    },

    endTut() {
        _GAME.utilsFB.log(`ev_done_tutorial`);

        // stop hand fx
        _GAME.ui.hideLayer('layer_tut');

        // stop highlight target cell
        const targetCell = cc.find(`Canvas/play_area/map/Image_HexBlock_${this.targetCell}/bg`);
        targetCell.opacity = targetCell.orgOpacity;

        // save to fb db that user done tut
        this.isTutDone = true;
        _GAME.data.saveData({ isTutDone: '1' });
    },


    checkHideTutHand() {
        if (this.isTutDone) return;
        const hand = cc.find('Canvas/layer_tut/handTutorial');
        hand.stopAllActions();
        hand.x = 9999;
    },

    checkShowTutHand() {
        if (this.isTutDone) return;
        const hand = cc.find('Canvas/layer_tut/handTutorial');
        _GAME.tut.startHandAction();
    },

    checkDoneTut() {
        if (this.isTutDone) return;
        this.endTut();
    },

}