import * as _GAME from '../all_modules';
import Share from "../social/share";
import Message from '../social/message';

export const social = {
    init() {
        _GAME.login.addLoginDataField('subscribeInfo');
        _GAME.login.addLoginDataField('shortcutInfo');
        _GAME.login.addCallback(data => {
            this.subscribeInfo = data.hasOwnProperty('subscribeInfo') ? data.subscribeInfo : 5;
            this.shortcutInfo = data.hasOwnProperty('shortcutInfo') ? data.shortcutInfo : 5;
        })
    },

    share(userScore) {
        if(cc.find('Canvas/shares').active == false) cc.find('Canvas/shares').active =true;
        _GAME.utils.setLabelText(
            cc.find('Canvas/shares/sharepicture/label_score'),
            _GAME.utils.localizeNumber(userScore)
        );
        cc.find('Canvas/shares').getComponent(Share).sharePostNormal();
    },
    sendMessageAfterQuit() {
        if(cc.find("Canvas/messages").active == false) cc.find("Canvas/messages").active = true;
        cc.find("Canvas/messages").getComponent(Message).sendMessagePlayWith();
    },

    askBotSubscribe(callback) {
        // _log(`this.subscribeInfo=${this.subscribeInfo}`);
        const finalCallback = () => callback && callback();
        if (!window.FBInstant) return finalCallback();
        if (!_GAME.utilsFB.isSupportedAPI("player.canSubscribeBotAsync") || !_GAME.utilsFB.isSupportedAPI("player.subscribeBotAsync")) return finalCallback() || _log(' askBotSubscribe :: isSupportedAPI > not supported');
        if (this.subscribeInfo == 'accepted' || this.subscribeInfo <= 0) return finalCallback();
        this.subscribeInfo--;
        _GAME.data.saveData({ subscribeInfo: this.subscribeInfo });

        FBInstant.player.canSubscribeBotAsync().then(can_subscribe => {
            if (!can_subscribe) return finalCallback() || _log(` askBotSubscribe :: can_subscribe = ${can_subscribe} `);
            FBInstant.player.subscribeBotAsync().then(() => {
                this.subscribeInfo = 'accepted';
                _GAME.data.saveData({ subscribeInfo: this.subscribeInfo });
                finalCallback();

            }).catch(e => finalCallback());
        }).catch(e => finalCallback() || _log(` askBotSubscribe :: canSubscribeBotAsync().then(fail) => `, e));
    },


    askShortcut(callback) {
        // _log(`this.shortcutInfo=${this.shortcutInfo}`);
        const finalCallback = () => callback && callback();
        if (!window.FBInstant) return finalCallback();
        if (!_GAME.utilsFB.isSupportedAPI("canCreateShortcutAsync") || !_GAME.utilsFB.isSupportedAPI("createShortcutAsync")) return finalCallback();
        if (this.shortcutInfo == 'accepted' || this.shortcutInfo <= 0) return finalCallback();
        this.shortcutInfo--;
        _GAME.data.saveData({ shortcutInfo: this.shortcutInfo });

        FBInstant.canCreateShortcutAsync().then(canCreateShortcut => {
            if (!canCreateShortcut) return finalCallback();
            FBInstant.createShortcutAsync().then(() => {
                this.shortcutInfo = 'accepted';
                _GAME.data.saveData({ shortcutInfo: this.shortcutInfo });
                finalCallback();

            }).catch((e) => finalCallback() || _log(' createShortcutAsync :: ', e));
        }).catch((e) => finalCallback() || _log(' canCreateShortcutAsync :: ', e));
    },


    postSessionScore() {
        if (!window.FBInstant) return;
        if (_GAME.user.score <= (_GAME.game.bestSessionScore || 0)) return;
        _GAME.game.bestSessionScore = _GAME.user.score;
        _log(` postSessionScore called score = ${_GAME.game.bestSessionScore}`);
        FBInstant.postSessionScore(_GAME.game.bestSessionScore);
    },


    createContext(playerId, callbackSuccess, callbackFail) {
        _log(` about to create context with player ${playerId}`);
        if (!window.FBInstant) {
            if (callbackSuccess) { callbackSuccess(); }
            return;
        }

        const logContextResult = (result, error) => {
            if (result) _log('Success ====> ', result);
            if (error) _log('Fail  ====> ', error);
        }
        FBInstant.context.createAsync(playerId).then(
            () => {
                if (callbackSuccess) { callbackSuccess(); }
                logContextResult('success');
            },
            (e) => {
                if (callbackFail) { callbackFail(e); }
                logContextResult('fail', e);
            }
        );
    },
    invite(callback) {
        try {
            FBInstant.context.chooseAsync().then(() => {
                // this.sendMessageAfterQuit();
                if (callback) callback(FBInstant.context.getID());
            }).catch(err => {
                _log(err);
                if (callback) callback()
            });
        } catch (errX) {
            _log(errX);
            if (callback) callback()
        }
    },
    switchPromoGame(idGame) {
        FBInstant.switchGameAsync(idGame).catch(function(e) {
            // Handle game change failure
            console("gameChangeError: ", e);
        });
    }
};