import * as _GAME from '../all_modules';


let uuidIndex = 1e5;

export const utils = {
    textureCaches: {},

    init() {
        _GAME.utils.canvasNode = cc.find('Canvas');
    },

    // overwrite default _GAME.utils.setTimeout to use schedule - which is paused when user switch to another tab. This will make all animation synced since ccAction is scheduling-base
    setTimeout(callback, timeInMillisecond = 0) {
        const target = { _id: uuidIndex++, __instanceId: uuidIndex };
        target.callback = () => { callback(target); }
        cc.director.getScheduler().schedule(target.callback, target, timeInMillisecond / 1000, 0, 0, false);
        return target;
    },

    clearTimeout(target) {
        if (!target || !target._id || !target.callback) return;
        cc.director.getScheduler().unschedule(target.callback, target);
    },

    addButtonComp(node, handlerFunc, isBubble = true, playSound = true) {
        // if(node._components.length != 0){
        //     console.log(_GAME.utils.getNodePath(node));               
        
        // }
        setTimeout(() => { // make this process run later to prevent node not initalized yet
            if (!node.getComponent('free_button_comp')) {
                node.addComponent('free_button_comp');
            }
            
            const butComp = node.addComponent(cc.Button);
            butComp.transition = isBubble ? cc.Button.Transition.SCALE : null;
            const eventHandler = new cc.Component.EventHandler();
            eventHandler.target = node;
            eventHandler.component = 'free_button_comp';
            eventHandler.handler = 'freeHandler';
            butComp.clickEvents.push(eventHandler);

            node.freeButtonHandlerFunc = () => {
                handlerFunc();
                _GAME.audio.playSoundClickButton();
            };
        });
    },


    getNodePath(node) {
        let pathArr = [node.name]
        let parent = node.parent;
        let safeCount = 0;
        while (parent && safeCount++ < 50) {
            if (!parent.parent) { break; }
            pathArr.push(parent.name);
            parent = parent.parent;
        }
        return pathArr.reverse().join('/');
    },


    getChildByPath(node, path) {
        try {
            let result = node;
            path.split('/').map(childName => { result = result.getChildByName(childName) });
            return result;
        } catch (e) {
            console.warn(` Error calling getChildByPath(${path}) : ${e.message} `);
            return null;
        }
    },


    getComponentName(mixedName) {
        return mixedName.split('<')[1].replace('>', '');
    },

    // --- convert pos of single node to global pos, counted to all of node's parents positions & scales
    setOrgPos(node) {
        if (!node) { return }
        if (!node.orgPos) { node.orgPos = node.getPosition(); }
        node.setPosition(node.orgPos);
    },


    getGlobalPosition(node) {
        let parent = node.parent,
            x = node.x,
            y = node.y,
            safeCount = 0;
        if (!parent) { _log(` parent=null // node = ${node} `); }
        while (parent && parent.name != 'Canvas' && safeCount++ < 100) {
            x = x * parent.scaleX + parent.x;
            y = y * parent.scaleY + parent.y;
            parent = parent.parent;
        }
        return cc.v2(x, y);
    },

    // --- get delta position that node must added to its local position to get given global position.
    // --- example : const posDiff = _GAME.utils.global2LocalPosDiff(ABC, 100, 200);   ABC.x += posDiff.x; ABC.y += posDiff.y;
    global2LocalPosDiff(node, gPosX, gPosY) {
        let parent = node.parent,
            parentArr = [],
            safeCount = 0;
        while (parent.name != 'Canvas' && safeCount++ < 100) {
            parentArr.push(parent);
            parent = parent.parent;
        }
        let x = gPosX,
            y = gPosY;
        parentArr.reverse().map(parent => {
            x = (x - parent.x) / parent.scaleX;
            y = (y - parent.y) / parent.scaleY;
        });
        return cc.v2(x - node.x, y - node.y);
    },

    setGlobalPositionToNode(sourceNode, targetNode) {
        const gPos = _GAME.utils.getGlobalPosition(targetNode);
        const posDiff = _GAME.utils.global2LocalPosDiff(sourceNode, gPos.x, gPos.y);
        sourceNode.x += posDiff.x;
        sourceNode.y += posDiff.y;
        return gPos;
    },


    /**
     * return random item from array & remove that item from array
     * @param {*} Arr 
     * @param {*} removeItem 
     */

    randomArrItem(Arr, removeItem = false) {
        var iRandom = _floor(_random() * (Arr.length));
        return (removeItem ? Arr.splice(iRandom, 1)[0] : Arr[iRandom]);
    },

    randomObjectProp: (obj, removeItem = false) => {
        var arr = Array.from(Object.keys(obj)),
            prop = _GAME.utils.randomArrItem(arr);
        var val = obj[prop];
        if (removeItem) { delete obj[prop]; }
        return val;
    },
    getRandomInt(min,max) {
            return min + Math.random() * (max - min);
    }, 
    /* get random value from set of N ratio among N values
    	input format = [
    		{ value: 'abc', ratio: 5 },
    		{ value: 'def', ratio: 15 },
    		{ value: 'ghi', ratio: 5 },
    		{ value: 'jkl', ratio: 6 },
    		etc
    	] */

    /* Example : 
		// test ================================================
        const dataX = [
			{ value: 'abc', ratio: 5 },
			{ value: 'def', ratio: 15 },
			{ value: 'ghi', ratio: 5 },
			{ value: 'jkl', ratio: 50 },
        ];
        const resultArr=[];
        for(let i = 0; i< 100; i++) resultArr.push( _GAME.utils.randomItemFromRatioSet( dataX ) );
        resultArr.sort((A,B)=> A.value > B.value ? 1 : -1 );
        console.table( resultArr );
        // test ================================================  */

    randomItemFromRatioSet(itemArr) {
        let lastSum = 0;
        const ratioScaleArr = [];
        itemArr.map(item => {
            lastSum += item.ratio;
            ratioScaleArr.push(lastSum);
        });
        const rnd = parseInt(_random() * lastSum);
        let keyIndex = 0;
        ratioScaleArr.some((val, index) => {
            if (rnd < val) {
                keyIndex = index;
                return true;
            }
        })
        return itemArr[keyIndex];
    },


    /**
     * return object {days, hours, minutes, seconds}
     * @param {number} secs seconds
     */
    secondsToTime(secs) {
        const days = Math.floor(secs / 86400);
        const hours = Math.floor((secs % 86400) / 3600);
        const minutes = Math.floor((secs % 3600) / 60);
        const seconds = secs % 60;
        return { days, hours, minutes, seconds };
    },

    /**
     * - return a time string
     * - example: 1 day or 2 hrs or 40 secs
     * @param {number} secs seconds
     */
    secondsToTimeString(secs) {
        const { days, hours, minutes, seconds } = this.secondsToTime(secs);
        if (days > 0) { return days + ' day' + (days > 1 ? 's' : ''); }
        if (hours > 0) { return hours + ' hr' + (hours > 1 ? 's' : ''); }
        if (minutes > 0) { return minutes + ' min' + (minutes > 1 ? 's' : ''); }
        return (seconds || 0) + ' sec' + (seconds > 1 ? 's' : '');
    },

    /**
     * - return a time string
     * - example: 1d or 01:11:01
     * @param {number} secs seconds 
     */
    secondsToTimeCountdown(secs) {
        if (secs <= 0) {
            return '00:00';
        }
        const { days, hours, minutes, seconds } = this.secondsToTime(secs);
        if (days > 0) { return days + ' day' + (days > 1 ? 's' : ''); }

        let ret = "";
        if (hours > 0) ret = ("0" + hours).slice(-2) + ':';
        ret += ("0" + minutes).slice(-2) + ':';
        ret += ("0" + seconds).slice(-2);
        return ret;
    },


    /**
     * - Example: template = 'time remaining xxx - please wait'
     * - xxx will be replaced with real time
     * @param {cc.Node} node 
     * @param {number} targetUTC 
     * @param {function} timeoutCallback 
     * @param {string} template 
     */
    setLabelCountDownTimer(node, targetUTC, timeoutCallback, template) {
        let N = node;
        if (_isString(N)) { N = cc.find(N); }
        if (N.countDownTimerVar) {
            clearInterval(N.countDownTimerVar);
        }
        const timerFunc = () => {
            if (!N.parent) {
                clearInterval(N.countDownTimerVar);
                return;
            }
            const currentTime = _time();
            const timeDiff = targetUTC - currentTime;
            const timeDiffStr = _GAME.utils.secondsToTimeCountdown(_floor(timeDiff / 1000));
            const str = (template || 'xxx').replace('xxx', timeDiffStr)
            _GAME.utils.setLabelText(N, str);

            if (timeDiff <= 0) {
                clearInterval(N.countDownTimerVar);
                if (timeoutCallback) {
                    timeoutCallback();
                }
            };
        }
        N.countDownTimerVar = setInterval(timerFunc, 500);
        timerFunc();
    },



    //--- wait for certain property of certain object to be true & call a callback
    waitToRun(callback, propertyName, mainObject = window, interval = 0.1, maxTimeWait, timeoutCallback) {
        let isRunSuccess = false;
        const waitInterval = setInterval(() => {
            if (propertyName.includes('()')) {
                if (!mainObject[propertyName.replace('()', '')]()) {
                    return;
                }
            } else if (!mainObject[propertyName]) {
                return;
            }
            clearInterval(waitInterval);
            isRunSuccess = true;
            callback();
        }, interval * 1000);

        if (maxTimeWait) {
            _GAME.utils.setTimeout(() => {
                clearInterval(waitInterval);
                if (timeoutCallback && !isRunSuccess) { timeoutCallback(); }
            }, maxTimeWait * 1000);
        }
    },

    extendObj: (orgObj, newObj, propArr) => {
        propArr.map((prop) => { newObj[prop] = orgObj[prop]; });
    },

    //======== set node properties
    setNodeProps: function(N, width, height, x, y, scale) {
        width && (N.width = width);
        height && (N.height = height);
        x && (N.x = x);
        y && (N.y = y);
        scale && (N.scale = scale);
    },

    preloadImage(imgPath, callback) {
        if (_GAME.utils.textureCaches[imgPath]) {
            return callback && callback(_GAME.utils.textureCaches[imgPath]);
        }

        cc.loader.loadRes(imgPath, cc.SpriteFrame, (e, texture) => {
            if (e) {
                // _log(` utils.preloadImage > Error loading image from resource ${imgPath} `, e);
                if (callback) callback(false);
            } else {
                _GAME.utils.textureCaches[imgPath] = texture;
                if (callback) callback(texture);
            }
        });
    },


    copyTexture(sourceNode, destNode) {
        const sComp = destNode.getComponent(cc.Sprite) || destNode.addComponent(cc.Sprite);
        sComp.spriteFrame = sourceNode.getComponent(cc.Sprite).spriteFrame;
    },


    /**
     * 
     * @param {cc.Node} node 
     * @param {string} url 
     */
    setNodeImgFromUrl(node, url) {
        node.isTextureFromURL = true;
        if (!_GAME.utils.textureCaches[url]) {
            _GAME.utils.textureCaches[url] = new cc.SpriteFrame(url);
        }
        _GAME.utils.setNodeImgFromSpriteFrame(node, _GAME.utils.textureCaches[url]);
    },

    /**
     * 
     * @param {cc.Node} node 
     * @param {string} imgPath 
     */
    setNodeImgFromResources(node, imgPath, isResetOrgSize = false, callback) {
        // _log(` setNodeImgFromResources :: start loading [${imgPath}] ... `);
        node.isTextureFromResource = true;
        node.currentTexturePath = imgPath;
        if (!_GAME.utils.textureCaches[imgPath]) {
            // node.getComponent(cc.Sprite).enabled = false;
            cc.loader.loadRes(imgPath, cc.SpriteFrame, (e, texture) => {
                if (e) {
                    return //_log(` Error loading image from resource ${imgPath} `, e);
                } else {
                    _GAME.utils.textureCaches[imgPath] = texture;
                    // _log(` setNodeImgFromResources :: DONE loading [${imgPath}] >>>> `);
                    if (node.currentTexturePath == imgPath) { // in case we set multiple textures to same node
                        node.getComponent(cc.Sprite).enabled = true;
                        _GAME.utils.setNodeImgFromSpriteFrame(node, _GAME.utils.textureCaches[imgPath], !isResetOrgSize, isResetOrgSize);
                        if (callback) callback();
                    }
                }
            });
        } else {
            // if(!_GAME.utils.textureCaches[imgPath].textureLoaded()){
            // 	console.warn(` ERROR loaded image [${imgPath}] >> textureLoaded() = false `);
            // }
            node.getComponent(cc.Sprite).enabled = true;
            _GAME.utils.setNodeImgFromSpriteFrame(node, _GAME.utils.textureCaches[imgPath], !isResetOrgSize, isResetOrgSize);
            if (callback) callback();
        }
    },

    /**
     * 
     * @param {cc.Node} node 
     * @param {cc.SpriteFrame} frame 
     * @param {boolean} keepOldSize 
     * @param {boolean} isSizedToNewSFrame 
     */
    setNodeImgFromSpriteFrame(node, frame, keepOldSize = true, isSizedToNewSFrame = false) {
        const orgW = node.width,
            orgH = node.height;
        if (!node.getComponent(cc.Sprite)) { node.addComponent(cc.Sprite); }
        // _log( `setNodeImgFromSpriteFrame > frame.textureLoaded() = ${frame.textureLoaded()}` );
        _GAME.utils.waitToRun(() => {
            // _log(` setNodeImgFromSpriteFrame > frame.textureLoaded() = true =>>>>>> [[node.getComponent(cc.Sprite).spriteFrame = frame]] `);
            node.getComponent(cc.Sprite).spriteFrame = frame;
            if (keepOldSize) {
                node.width = (orgW != 0) ? orgW : frame._originalSize.width;
                node.height = (orgH != 0) ? orgH : frame._originalSize.height;
            } else {
                if (frame._originalSize.width != 0) {
                    node.width = frame._originalSize.width;
                }
                if (frame._originalSize.height != 0) {
                    node.height = frame._originalSize.height;

                }
            }
            if (isSizedToNewSFrame) {
                const sFrame = node.getComponent(cc.Sprite).spriteFrame;
                node.width = sFrame._originalSize.width;
                node.height = sFrame._originalSize.height;
            }
        }, 'textureLoaded()', frame);
    },

    /**
     * 
     * @param {cc.Node} node 
     * @param {*} img 
     * @param {boolean} keepOldSize 
     * @param {boolean} isSizedToNewSFrame 
     */
    setNodeImg(node, img, keepOldSize = true, isSizedToNewSFrame = false) {
        if (typeof img === "string") {
            // _log(` img = ${img} //  this.isURL(img)=${this.isURL(img)}`);
            if (this.isURL(img)) {
                _GAME.utils.setNodeImgFromUrl(node, img);
            } else {
                _GAME.utils.setNodeImgFromResources(node, img, isSizedToNewSFrame);
            }
        } else if (img instanceof cc.SpriteFrame) {
            // _log(` img.constructor.name == "cc_SpriteFrame" `);
            _GAME.utils.setNodeImgFromSpriteFrame(node, img, keepOldSize, isSizedToNewSFrame);
        } else if (img instanceof cc.Node) {
            // _log(` img.constructor.name == "cc.Node" `);
            const sComp = img.getComponent(cc.Sprite);
            // _log(`  sComp = `, sComp)
            if (!sComp) return;
            _GAME.utils.setNodeImgFromSpriteFrame(node, sComp.spriteFrame, keepOldSize, isSizedToNewSFrame);
        } else {
            // console.warn(` setNodeImg > undefined image format > img.constructor = ${img.constructor.name}`);
        }
    },

    /**
     * 
     * @param {cc.Node} label 
     * @param {string} str 
     */
    setLabelText(label, str) {
        if (!label) { return; }
        if(typeof str == 'string'){
            // console.log(str,'===>',typeof str);
            if(typeof str == 'number') {
                label.getComponent(cc.Label).string = str;
                return;
            }
            let newStr = '';
            for (let index = 0; index < str.length; index++) {
                let element = str[index];
                if(element != ' '){
                    newStr += element;
                }
            }
            // console.log(newStr,'===>',typeof newStr);
            let valueCompare = newStr.toUpperCase();
            let value =_GAME.localize.convertPosition(valueCompare);
            if(value.length != 0){
                // console.log('==>',value);
                label.getComponent(cc.Label).string = value;
            }else{
                label.getComponent(cc.Label).string = str;
            }
        }else{
            label.getComponent(cc.Label).string = str;
        }
        
    },

    /**
     * 
     * @param {cc.Node} label 
     * @param {string|number} num 
     */
    setLabelTextBigNumber(label, num) {
        if (!label) { return; }
        if (isNaN(num)) { num = 0; }
        label.getComponent(cc.Label).string = _GAME.utils.formatCurrency(num, 999999999999999);
    },

    /**
     * 
     * @param {cc.Node} label 
     * @param {string|number} number 
     * @param {number} maxSize 
     */
    setLabelTextPrice(label, number, maxSize) {
        if (!label) { return; }
        const isStartedWPlus = _isString(number) && number.startsWith('+')
        const num = isStartedWPlus ? parseInt(number.replace('+', '')) : number;
        let str = _GAME.utils.formatCurrency(num, maxSize);
        label.getComponent(cc.Label).string = (isStartedWPlus ? '+' : '') + str;
    },
    setLabelTextShotcut: function(node, text) {
        if (node) {
            if (!text.length) return;
            let newText = (text.length > 6) ? text.slice(0, 6) : text;
            let compNode = node.getComponent(cc.Label);
            compNode.string = newText + '...';
        }
    },

    // move a node to another parent
    moveNodeTo: function(node, newParent, copy = false) {
        var N = (copy ? cc.instantiate(node) : node);
        if (newParent) {
            if (N.parent != newParent) {
                N.removeFromParent(false);
                newParent.addChild(N);
            }
        }
        return N;
    },

    copyNodeTo: (node, newParent) => {
        var N = _GAME.utils.moveNodeTo(node, newParent, true);

        //=== maintain size of original nodes if copy (when copy, new node is created & set spriteFrame --> size is changed to spriteFrame size)
        var originalSComps = node.getComponentsInChildren(cc.Sprite);
        var newSComps = N.getComponentsInChildren(cc.Sprite);
        originalSComps.map((comp, i) => {
            var orgNode = comp.node,
                newNode = newSComps[i].node;
            _GAME.utils.extendObj(orgNode, newNode, ['width', 'height']);
            if (orgNode.onTouchEnd) { newNode.on('touchend', orgNode.onTouchEnd); }
        });

        return N;
    },


    prefabToNode(name) {
        const N = cc.instantiate(_GAME.prefabList[name]);
        return N;
    },

    preloadPrefabToNode(prefabName, nodeName, parent) {
        const node = _GAME.utils.prefabToNode(prefabName);
        node.name = nodeName;
        node.parent = parent;
        node.setPosition(0, 0);
        node.active = false;
    },

    /**
     * return a string in 'en' format
     * @param {number} value 
     */
    formatNumber(value) {
        return value.toLocaleString('en');
    },

    /**
     * return a string of currency that formatted
     * @param {number} value input value
     * @param {number} maxSize maxsize of 99..9
     */
    formatCurrency(value, maxSize) {
        const SUFFIXES = ['K', 'M', 'B', 'T', 'aa', 'ab', 'ad', 'ae', 'af'];
        let x = Math.floor(value);
        let endValueStr = "";
        if (x > maxSize) {
            for (let suffix of SUFFIXES) {
                x /= 1000;
                if (x < maxSize) {
                    endValueStr = suffix;
                    break;
                }
            }
        }

        return x.toFixed(0).replace(',', '.').replace(/(\d)(?=(\d{3})+(?!\d))/g, '$1,') + endValueStr;
    },

    isURL(str) {
        var pattern = new RegExp('^(https?:\\/\\/)?' + // protocol
            '((([a-z\\d]([a-z\\d-]*[a-z\\d])*)\\.)+[a-z]{2,}|' + // domain name
            '((\\d{1,3}\\.){3}\\d{1,3}))' + // OR ip (v4) address
            '(\\:\\d+)?(\\/[-a-z\\d%_.~+]*)*' + // port and path
            '(\\?[;&a-z\\d%_.~+=-]*)?' + // query string
            '(\\#[-a-z\\d_]*)?$', 'i'); // fragment locator
        return !!pattern.test(str);
    },


    singleTouchSet(N, touchStartFunc, touchMoveFunc, touchEndFunc) {
        const scrW2 = cc.winSize.width / 2;
        const scrH2 = cc.winSize.height / 2;
        const callFuncWithEvent = (func, event) => {
            const pos = event.touch.getLocation();
            const realPos = cc.v2(pos.x - scrW2, pos.y - scrH2);
            func(realPos, event);
        }

        // TEST MULTI-TOUCHES => GET TOUCH ID
        N.on('touchstart', (event) => {
            if (N.touchingID) { return; }
            N.touchingID = event.touch._id + 1;
            touchStartFunc && callFuncWithEvent(touchStartFunc, event);
            // _log(` touchstart ID = ${N.touchingID} // target_name = ${event.target.name} // event.touch= `, event.touch);
        });

        touchMoveFunc && N.on('touchmove', (event) => {
            var tID = event.touch._id + 1;
            if (tID == N.touchingID) {
                touchMoveFunc && callFuncWithEvent(touchMoveFunc, event);
            }
            // _log(` touchMove ID = ${tID} `, true);
        });

        var touchDestroy = (event) => {
            var tID = event.touch._id + 1;
            if (tID == N.touchingID) {
                N.touchingID = null;
                touchEndFunc && callFuncWithEvent(touchEndFunc, event);
            }
            // _log(` touchEnd ID = ${tID} `, true);
        }

        N.on('touchend', touchDestroy);
        N.on('touchcancel', touchDestroy);
    },


    removeTouchHandler(node) {
        node.isDraggable = false;
        node.touchStartHandlerArr = node.touchMoveHandlerArr = node.touchEndHandlerArr = null;
        node.off('touchstart');
        node.off('touchend');
        node.off('touchmove');
        node.off('touchcancel');
    },


    localizeNumber: (text) => {
        let local = '';
        local = window.FBInstant ? FBInstant.getLocale() : 'en_US';

        let txtFormat = '.';
        switch (local) {
            case 'en_US':
            case 'en_GB':
            case 'th_TH':
                txtFormat = ',';
                break;
            case 'fr_FR':
            case 'fr_CA':
            case 'ru_RU':
                txtFormat = ' ';
                break;
            case 'pl_PL':
            case 'it_IT':
            case 'hu_HU':
                txtFormat = '';
                break;
        }

        // console.log(local);
        // console.log(txtFormat);

        let s = "";
        let g = text;
        let count = 0;
        let c = "";
        while (g >= 10) {
            if (count > 0 && count % 3 == 0) {
                s = txtFormat + s;
            }
            let i = g % 10;
            c = i + '';
            s = c + s;
            g = (g - i) / 10;
            count++;
        }
        if (count > 0 && count % 3 == 0) {
            s = txtFormat + s;
        }
        c = g + '';
        s = c + s;
        return s;
    }


}