import * as _GAME from '../all_modules';

export const leaderboard = {
    leaderboard: null,
    globalPlayerArr: [],
    isEntriesLoadedGlobal: false,
    friendsPlayerArr: [],
    isEntriesLoadedFriends: false,

    init() {
        // fake data
        let self = this;
        if (!window.FBInstant) {
            self.globalPlayerArr = [
                { id: 'xxxxaa111x', name: 'Global 123', score: 222 },
                { id: 'xxxxaa111x', name: 'Global 456', score: 676 },
                { id: 'xxxxaa111', name: 'Global', score: 1235 },
                { id: 'xxxxaa111x', name: 'Global 789', score: 4354 },
                { id: 'xxxxaa111x', name: 'Global 100', score: 3455 },
                { id: 'xxxxaa111x', name: 'Global 456', score: 676 },
                { id: 'xxxxaa111', name: 'Global', score: 1235 },
                { id: 'xxxxaa111x', name: 'Global 789', score: 4354 },
                { id: 'xxxxaa111x', name: 'Global 100', score: 3455 },
                { id: 'xxxxaa111x', name: 'Global 456', score: 676 },
                { id: 'xxxxaa111', name: 'Global', score: 1235 },
                { id: 'xxxxaa111x', name: 'Global 789', score: 4354 },
                { id: 'xxxxaa111x', name: 'Global 100', score: 3455 },
            ];
            _GAME.leaderboard.isEntriesLoadedGlobal = true;

            self.friendsPlayerArr = [
                { id: 'xxxxaa111x', name: 'Friend 123', score: 222 },
                { id: 'xxxxaa111x', name: 'Friend 456', score: 676 },
                { id: 'xxxxaa111', name: 'Friend', score: 1235 },
                { id: 'xxxxaa111x', name: 'Friend 789', score: 4354 },
                { id: 'xxxxaa111x', name: 'Friend 100', score: 3455 },
                { id: 'xxxxaa111x', name: 'Friend 456', score: 676 },
                { id: 'xxxxaa111', name: 'Friend', score: 1235 },
                { id: 'xxxxaa111x', name: 'Friend 789', score: 4354 },
                { id: 'xxxxaa111x', name: 'Friend 100', score: 3455 },
                { id: 'xxxxaa111x', name: 'Friend 456', score: 676 },
                { id: 'xxxxaa111', name: 'Friend', score: 1235 },
                { id: 'xxxxaa111x', name: 'Friend 789', score: 4354 },
                { id: 'xxxxaa111x', name: 'Friend 100', score: 3455 },
            ];
            _GAME.leaderboard.isEntriesLoadedFriends = true;
        }

        // real data
        else {
            FBInstant.getLeaderboardAsync(_GAME.configs.leaderboardName).then(leaderboard => {
                self.leaderboard = leaderboard;
                // _log('this.leaderboard   ', leaderboard);
                self.leaderboard.getPlayerEntryAsync().then(entry => {
                    // _log('entry    ', entry);
                    if (!entry) {
                        self.leaderboard.setScoreAsync(_GAME.user.exp, "").then(() => self.getEntries());
                    } else self.getEntries();
                });

            }).catch(err => _log(` error getting leaderboard `, err));
            // _log('get Real Data    ', _GAME.leaderboard);
        }

        // render

        _GAME.utils.waitToRun(() => self.render('friends'), 'isEntriesLoadedFriends', _GAME.leaderboard);
        _GAME.utils.waitToRun(() => self.render('global'), 'isEntriesLoadedGlobal', _GAME.leaderboard);
    },




    getEntries() {
        // global
        this.leaderboard
            .getEntriesAsync(_GAME.configs.leaderboardPlayerCount, 0)
            .then(entries => {
                this.globalPlayerArr = this.entriesToArr(entries);
                _GAME.leaderboard.isEntriesLoadedGlobal = true;
            })
            .catch(err => _log(`Error getting leaderboard global entries `, err));

        // friends
        this.leaderboard
            .getConnectedPlayerEntriesAsync(_GAME.configs.leaderboardPlayerCount, 0)
            .then(entries => {
                this.friendsPlayerArr = this.entriesToArr(entries);
                _GAME.leaderboard.isEntriesLoadedFriends = true;
            })
            .catch(err => _log(`Error getting leaderboard global entries `, err));
    },

    entriesToArr(entries) {
        const playerArr = []
        for (let i = 0; i < entries.length; i++) {
            const entry = entries[i];
            playerArr.push({
                id: entry.getPlayer().getID(),
                photo: entry.getPlayer().getPhoto(),
                name: entry.getPlayer().getName(),
                score: entry.getScore(),
                rank: entry.getRank(),
            })
        }
        return playerArr;
    },


    saveScore() {
        _GAME.ui.showNagScreen();
        this.leaderboard.setScoreAsync(_GAME.user.score, "").then(() => {
            _log(`saveScore :: >>> saved score to leaderboard-- - _GAME.user.exp = ${_GAME.user.score} `);
            // _log('_GAME.user.bestScore     ', _GAME.user.bestScore, '_GAME.user.score     ', _GAME.user.score);
            if (_GAME.user.bestScore <= _GAME.user.score) {
                _log('_GAME.user.bestScore < _GAME.user.scoree     ');
                _GAME.leaderboard.isEntriesLoadedFriends = false;
                _GAME.leaderboard.isEntriesLoadedGlobal = false;
                _GAME.leaderboard.init();
            }
            if (_GAME.social.playerIdSendMessage) {
                _GAME.social.sendMessageAfterQuit();

            }
            _GAME.promo.renderButtonByIcon();
            _GAME.ui.hideNagScreen();
            // if user has new best => reload leaderboard
        }).catch(err => _log(`saveScore err `, err));




    },

    render(leaderboardName = 'friends') {
        let playerArr = _GAME.leaderboard[`${leaderboardName}PlayerArr`];
        // _log(` leaderboard >> render called // leaderboardName=${leaderboardName} // playerArr= `, playerArr);

        const sampleRow = cc.find('Canvas/layer_leaderboard/content_bg/middle_container/leaderboard/leaderboard_content/sample_row');
        const container = cc.find(`Canvas/layer_leaderboard/content_bg/middle_container/leaderboard/leaderboard_content/scrollview_${leaderboardName}/view/content`);
        container.removeAllChildren();
        // _log('N  n on gen li      ', leaderboardName);
        playerArr.map((player, index) => {
                const newRow = _GAME.utils.copyNodeTo(sampleRow, container);

                newRow.active = true;
                // bg
                newRow.getChildByName('bg2').active = (index % 2); //alternative bg

                // photo border
                // for (let i = 0; i < 3; i++) _GAME.utils.getChildByPath(newRow, `avatar/LeaderBoardStrokeAvatar_${i + 1}`).active = (i == index); //avatar bg

                // avatar
                if (player.photo) {
                    cc.loader.load(player.photo, (err, texture) => {
                        if (err) return _log(err);
                        const avarNode = _GAME.utils.getChildByPath(newRow, 'avatar/real_avatar');
                        const avarNodeOrgSize = { width: avarNode.width, height: avarNode.height };
                        avarNode.getComponent(cc.Sprite).spriteFrame = new cc.SpriteFrame(texture);

                        // **** reset size of node because STUPID FUCKING COCOS will set node size to size of image loaded from url no matter what 
                        avarNode.setSizeFunc = () => {
                            avarNode.width = avarNodeOrgSize.width;
                            avarNode.height = avarNodeOrgSize.height;
                        }
                        setTimeout(avarNode.setSizeFunc, 100);
                    });
                }

                // text
                _GAME.utils.setLabelText(newRow.getChildByName('label_rank'), (index + 1) + '.');
                _GAME.utils.setLabelText(newRow.getChildByName('label_user_name'), player.name);
                _GAME.utils.setLabelText(newRow.getChildByName('label_score'), _GAME.utils.localizeNumber(player.score));

                // buttons
                newRow.getChildByName('btn_brag').active = (player.id == _GAME.user.id);
                newRow.getChildByName('btn_play').active = (leaderboardName == 'friends');

                _GAME.utils.addButtonComp(
                    newRow.getChildByName('btn_brag'),
                    () => {
                        _GAME.utilsFB.log('ev_click_button_share_in_leader_board', 1, { Position: index });
                        _GAME.social.share(player.score);
                    }
                );

                _GAME.utils.addButtonComp(
                    newRow.getChildByName('btn_play'),
                    () => {
                        _GAME.utilsFB.log('ev_click_button_play_in_leader_board', 1, { Position: index });
                        // _GAME.social.invite();
                        _GAME.social.createContext(
                            player.id,
                            () => {
                                _GAME.social.playerIdSendMessage = player.id;
                                _log('Successs    ');
                                _GAME.ui.hideLayer('layer_leaderboard');
                                // _GAME.interAd.show('gameOverReplay');
                                _GAME.game.replay(() => _GAME.interAd.checkRequireMentShowAdd(() => _GAME.interAd.show('gameOverReplay')));
                            },
                            () => {
                                _log('fail  ');
                            }
                        )
                    }
                );
            })
            // _log('---------------------------------');
            // _log('container   active  ', container.active);
            // container.children.map((child) => {
            //     _log('child   acitve  ', child.active);
            // });

    },



    btnBragClick() {

    },

    //  tab switching
    currentTabName: 'friends',
    onClickTab(tabName) {
        if (tabName == this.currentTabName) return;
        this.currentTabName = tabName;
        const setTabActive = (myTabName, isActive) => {
                // the tab
                cc.find(`Canvas/layer_leaderboard/content_bg/middle_container/leaderboard/tabs/${myTabName}/active`).active = isActive;

                // the leaderboard content
                cc.find(`Canvas/layer_leaderboard/content_bg/middle_container/leaderboard/leaderboard_content/scrollview_${myTabName}`).active = isActive;
            }
            ['global', 'friends'].map(name => setTabActive(name, (name == tabName)));

        // HANDLE THE FUCKING RESIZE BUG OF COCOS 2.4 WITH IMAGE LOADED FROM URL
        cc.find(`Canvas/layer_leaderboard/content_bg/middle_container/leaderboard/leaderboard_content/scrollview_${tabName}/view/content`).children.map(row => {
            const setSizeFunc = _GAME.utils.getChildByPath(row, 'avatar/real_avatar').setSizeFunc;
            if (setSizeFunc) setSizeFunc();
        })
    },

    updateScoreLabel() {
        _GAME.utils.setLabelText(
            cc.find('Canvas/layer_leaderboard/header/label_score'),
            _GAME.utils.localizeNumber(_GAME.user.score)
        )
    },

}