import * as _GAME from '../all_modules';

const loginDataFieldArr = [
    'LoginTime',
    'IsOldUser',
    'isTutDone',
    'isDoneTutRolated',
    'isDoneTutBom',
    'isDoneTutRelocated',
    'isDoneTutTriple'
];
const callbackArr = [];

export const login = {
    init() {
        _GAME.utils.waitToRun(() => {
            _GAME.data.loadEntryPointData();
            _GAME.login.getFBData();
        }, 'isUserReady', _GAME.data);
    },


    addLoginDataField(fieldName) {
        if (!loginDataFieldArr.includes(fieldName)) {
            loginDataFieldArr.push(fieldName);
        }
    },


    addCallback(callbackFunc) {
        if (!_GAME.login.loginData) {
            callbackArr.push(callbackFunc);
        } else {
            callbackFunc(_GAME.login.loginData);
        }
    },

    //  ==============================================================================================
    //  logic to handle input data from real_server / facebook
    //  ==============================================================================================

    getFBData() {
        _GAME.data.loadData(loginDataFieldArr, (data) => {
            data.isNewUser = !data.IsOldUser;

            if (data.isNewUser) {
                _GAME.utilsFB.log('ev_login_new_user_incoming');
                _GAME.user.initNewUser(data); // check to handle data for new user
                _GAME.login.initLogin(data);
                _GAME.data.saveData({ 'IsOldUser': true });
                _GAME.friend.loadDataFrienld();
                return;
            }
            // _log(`login :: getFBData :: data =  `, data);
            _GAME.login.initLogin(data);
            _GAME.friend.loadDataFrienld();
        });
    },


    //  ==============================================================================================
    //  logic to init all features 
    //  ==============================================================================================
    initLogin(data) {
        // call initLogin only once
        if (_GAME.login.isInitLoginCalled) { return; }
        _GAME.login.isInitLoginCalled = true;
        _GAME.login.loginData = data;
        _GAME.user.lastLoginTime = data.LoginTime || _time();

        // call all the loginCallback
        callbackArr.map((func, index) => {
            try {
                func(data);
            } catch (e) {
                _log('loginCallback func error', e);
            }
        });

    },


}