import * as _GAME from '../all_modules';
cc.Class({
    extends: cc.Component,

    properties: {
    },
    onLoad(){
        const bg_real = cc.find('Canvas/bg');
        _GAME.utils.setTimeout(()=>{
            bg_real.active =true;
            const nodeBgLoad2 = cc.find('Canvas/bg_load2');
            nodeBgLoad2.active = true;
            console.log('load bg2');
            _GAME.utils.setTimeout(() => {
                nodeBgLoad2.active = false;
            }, 5000);
        },1000);
        _GAME.utils.setTimeout(()=>{
            const nodeLoad2 = cc.find('Canvas/load2');
            
            const nodeShare = cc.find('Canvas/shares');
            const nodeMessage = cc.find('Canvas/messages');
            nodeLoad2.active = true;
           
            nodeShare.active = true;
            nodeMessage.active = true;
            _GAME.utils.setTimeout(() => {
                nodeLoad2.active = false;
                nodeShare.active = false;
                nodeMessage.active = false;
            }, 2000);
        },5000);
    }
});
