import * as _GAME from '../all_modules'

export const tutRolated = {
    
    init(){
        _GAME.login.addCallback(data => {
            this.isDoneTutRolated = data.isDoneTutRolated;
        });
    },
    checkDoneTutRolated(){
        if(this.isDoneTutRolated) return true;
        return false;
    },
    startTut(){
        if(_GAME.configs.off_power_item)return;
        if(this.showing) return;
        this.showing = true;
        _GAME.utilsFB.log(`ev_start_tut_rolated`,1);
        _GAME.ui.showLayer('layer_tut');
        _GAME.tutRolated.startHandAction();
        _GAME.tutRolated.showTextHinding();
    },
    startHandAction(){
        // console.log('vao day cha ');
        const hand = cc.find('Canvas/layer_tut/handTutorial');
        hand.stopAllActions();
        _GAME.utils.setGlobalPositionToNode(hand, cc.find('Canvas/play_area/new_item_container'));
        _GAME.utils.setGlobalPositionToNode(cc.find('Canvas/layer_tut/text_motivate'),cc.v2(hand.x,hand.y+150));
       
        hand.runAction(
            cc.repeatForever(
                cc.sequence(
                    cc.moveTo(1,cc.v2(hand.x+50,hand.y-50)),
                    cc.moveTo(1,cc.v2(hand.x,hand.y))
                )
            )
        );
    },
    endTut() {
        if(_GAME.configs.off_power_item)return;
        _GAME.utilsFB.log(`ev_end_tut_rolated`,1);
        // stop hand fx
        _GAME.tutRolated.setTextToMotivate(()=>{
            _GAME.ui.hideLayer('layer_tut');

            // stop highlight target cell
            // const targetCell = cc.find(`Canvas/play_area/map/Image_HexBlock_${this.targetCell}/bg`);
            // targetCell.opacity = targetCell.orgOpacity;
            this.showing = false;
            // save to fb db that user done tut
            this.isDoneTutRolated = true;
            _GAME.data.saveData({ isDoneTutRolated: '1' });
        });
    },


    checkHideTutHand() {
        if (this.isDoneTutRolated) return;
        const hand = cc.find('Canvas/layer_tut/handTutorial');
        hand.stopAllActions();
        hand.x = 9999;
    },

    checkShowTutHand() {
        if (this.isDoneTutRolated) return;
        const hand = cc.find('Canvas/layer_tut/handTutorial');
        _GAME.tut.startHandAction();
    },
    showTextHinding(){
        let nodeLabel = cc.find('Canvas/layer_tut/hinding');
        if(nodeLabel.active == false) nodeLabel.active = true;
        _GAME.utils.setLabelText(nodeLabel,'Tab here to switch block');
    },
    hideTextHiding(){
        let nodeLabel = cc.find('Canvas/layer_tut/hinding');
        if(nodeLabel.active == true) nodeLabel.active = false;
    },
    setTextToMotivate(callback){
        let textMotivate = cc.find('Canvas/layer_tut/text_motivate');
        let newNodeMotivate = cc.instantiate(textMotivate);
        _GAME.utils.setLabelText(newNodeMotivate,'GOOD');
        if(newNodeMotivate.active == false)newNodeMotivate.active = true;
        newNodeMotivate.parent = textMotivate.parent;
        newNodeMotivate.runAction(
            cc.sequence(
                cc.spawn(
                    cc.scaleTo(0.5,2),
                    cc.fadeTo(0.5,255)
                ),
                cc.spawn(
                    cc.delayTime(0.6),     
                    cc.sequence(
                        cc.scaleTo(0.15,2.5),
                        cc.scaleTo(0.15,2),
                        cc.scaleTo(0.15,2.5),
                        cc.scaleTo(0.15,2),
                    )
                ),
                cc.spawn(
                    cc.moveTo(0.3,cc.v2(newNodeMotivate.x,newNodeMotivate.y+100)),
                    cc.fadeTo(0.3,0)
                ),
                cc.callFunc(()=>{
                    newNodeMotivate.destroy();
                    if(callback)callback();
                })
            )
        )
    }
}