import * as _GAME from '../all_modules';

export const interAd = {
    init() {
        this.preloadInterAds();
    },

    interAdObj: null,
    preloadInterAds() {
        if(!_GAME.configs.canShowAndLoadInter) return;s
        if (!window.FBInstant) return
        _log(` preloadInterAds 1111 `);
        if (this.isLoading) return;

        this.interAdObj = null;
        this.isLoading = true;

        FBInstant.getInterstitialAdAsync(_GAME.configs.interAdId).then(interAdObj => {
            interAdObj.loadAsync().then(() => {
                this.interAdObj = interAdObj;
                this.isLoading = false;
            }).catch((e) => {
                console.warn(e);
                this.isLoading = false;
            });
        }).catch((e) => {
            console.warn(e);
            this.isLoading = false;
        });
    },

    showInterAds(adPosition, callBack) {
        if(!_GAME.configs.canShowAndLoadInter) return;s
        console.log(` showInterAds 1111 `);
        if (!_GAME.configs.openInterAds) {
            callBack && callBack();
            return;
        }
        if (!window.FBInstant || !this.interAdObj) return callBack && callBack();

        this.interAdObj.showAsync().then(() => {
            _GAME.interAd.log(adPosition, 1);
            if (callBack) callBack()
            this.preloadInterAds() // load another ads
        }).catch((e) => {
            console.warn(e);
            _GAME.interAd.log(adPosition, 0, e.code);
            callBack && callBack();
            if (e.code != 'RATE_LIMITED') this.preloadInterAds();
        })
    },

    log(adPosition, result, err_code = '') {
        // _log(` .... interstitial loggggg ... adPosition=${adPosition} // err_code= `, err_code);
        _GAME.utilsFB.log(`ev_inter_ad`, 1, {
            position: adPosition,
            watched_success: result,
            err_code: err_code
        });
        if (result) {
            _GAME.utilsFB.log(`Ads-Total-Inter`);
            _GAME.utilsFB.log(`Ads-Total`);
        }
    },


    // ===================================
    replayCount2ShowAd: 0,
    checkToShowReplayAd() {
        this.replayCount2ShowAd++;
        if (this.replayCount2ShowAd % _GAME.configs.showInterAdAfterEveryReplayTime == 1) {
            this.showInterAds('gameOverReplay');
        }
    },

}