const EXTRA_POS = cc.v2(0, 150);
import * as _GAME from '../all_modules';
cc.RotateBy._reverse = true;
export const control = {
    newItemNode: null,

    init() {
        const newItem = this.newItemNode = cc.find('Canvas/play_area/new_item_container/new_item');
        const posDiff = _GAME.utils.global2LocalPosDiff(newItem, 0, 0);
        let lastTouchStartPos;
        let lastTouchStartTime;

        const touchStartFunc = (pos, eventObj) => {
            // console.log(`touchStartFunc called, pos = ${pos}`);
            newItem.isBeingDragged = true;
            lastTouchStartPos = pos;
            lastTouchStartTime = _time();
        }

        const touchMoveFunc = (pos, eventObj) => {
            // console.log(`touchMoveFunc called, pos = ${pos}`);
            // console.log('pos.sub(lastTouchStartPos).mag()  ',pos.sub(lastTouchStartPos).mag());
            if (pos.sub(lastTouchStartPos).mag() < 20) return;
            this.setArrowsActive(false);
            _GAME.tut.checkHideTutHand();
            if (newItem.isBeingDragged) newItem.position = pos.add(posDiff).add(EXTRA_POS);
        }

        const touchEndFunc = (pos, eventObj) => {
            // console.log(`touchEndFunc called, pos = ${pos}`);
            newItem.isBeingDragged = false;
            newItem.position = cc.v2(0, 0);
            _GAME.tut.checkShowTutHand();
            this.setArrowsActive(false);

            // handle touchEvent to swap block
            if (
                _GAME.game.currentItemBiasType != 'single_block'
                && lastTouchStartPos.sub(pos).mag() < 10
                && _time() - lastTouchStartTime < 400
                && newItem.position.mag() < 10
            ) {
                if(!_GAME.tutRolated.checkDoneTutRolated()){
                    _GAME.tutRolated.endTut();
                }
                _GAME.game.swapNewItemBlocks();
            }

            // handle drop new item to map
           
            if (_GAME.map.selectedCell) 
            {
                _GAME.game.dropNewItemAtCell(
                    _GAME.map.selectedCell.cellNumber,
                    (_GAME.map.minorCell ? _GAME.map.minorCell.cellNumber : null));
            }
        }

        _GAME.utils.singleTouchSet(newItem, touchStartFunc, touchMoveFunc, touchEndFunc);
    },

    setArrowsActive(isActive) {
        const arrows = cc.find(`Canvas/play_area/new_item_container/new_item/arrows`);
        arrows.active = isActive;
        return arrows;
    },


    setKeyBlock(blockNode) {
        const isParentRotated = _abs(blockNode.parent.angle % 360) > 90;
        const posExtra = isParentRotated ? blockNode.position.mul(2) : cc.v2()
        const keyBlockCollider = cc.find('Canvas/play_area/new_item_container/new_item/key_block_collider');
        _GAME.utils.setGlobalPositionToNode(keyBlockCollider, blockNode);
        keyBlockCollider.x -= posExtra.x;
        keyBlockCollider.y -= posExtra.y;
    },


    setControlTouchArea(itemType) {
        const touchNode = cc.find('Canvas/play_area/new_item_container/new_item');
        switch (itemType) {
            case 'single_block':
                {
                    touchNode.width = 280;
                    touchNode.height = 300;
                    break;
                }

            case 'left_bias':
            case 'right_bias':
                {
                    touchNode.width = 240;
                    touchNode.height = 380;
                    break;
                }

            case 'horizontal':
                {
                    touchNode.width = 300;
                    touchNode.height = 300;
                    break;
                }
        }
    },

}
