import * as _GAME from '../all_modules';

// free component with open handler to help adding button component in script (_GAME.utils.addButtonComp)
cc.Class({
    extends: cc.Component,

    freeHandler(e) {
        if (e.target.freeButtonHandlerFunc) {
            e.target.freeButtonHandlerFunc(e.target);
            _GAME.audio.playSoundClickButton();
        }
    },

});