import * as _GAME from '../all_modules';

cc.Class({
    extends: cc.Component,
    properties: {
        collidingCellArr: [],
        newItemPosDiff: cc.v2(0, 0),
        cellPosDiff: cc.v2(0, 0),
        highlightCellArr: [],
    },

    onLoad() {
        const manager = cc.director.getCollisionManager();
        manager.enabled = true;
    },

    onCollisionEnter(other, self) {
        this.collidingCellArr.push(other.node);
    },

    onCollisionStay(other, self) {
        const newItemPosDiff = _GAME.utils.global2LocalPosDiff(this.node, 0, 0);

        let selectedCell;
        let nearestDistance = 999;
        this.collidingCellArr.map(cell => {
            const distance = _GAME.utils.global2LocalPosDiff(cell, 0, 0).sub(newItemPosDiff).mag();
            if (distance < nearestDistance) {
                nearestDistance = distance;
                selectedCell = cell;
            }
        });

        const cellNumber = selectedCell.name.replace('Image_HexBlock_', '');
        const minorCellNumber = _GAME.map.checkNewItemDroppableAtCell(cellNumber)
        if (minorCellNumber) {
            this.unfocusAllCells();
            this.highlightCell(selectedCell);
            _GAME.map.selectedCell = selectedCell;
            if (_GAME.game.currentItemBiasType != 'single_block') {
                const minorCell = _GAME.map.minorCell = cc.find(`Canvas/play_area/map/Image_HexBlock_${minorCellNumber}`);
                this.highlightCell(minorCell);
            }
        }
    },

    onCollisionExit(other, self) {
        if (_GAME.map.selectedCell) this.unfocusCell(_GAME.map.selectedCell);
        if (_GAME.map.minorCell) this.unfocusCell(_GAME.map.minorCell);
        _GAME.map.selectedCell = null;
        _GAME.map.minorCell = null;
        _removeArrItem(this.collidingCellArr, other.node);
    },

    //------- highlight & unfocus cells ----------------
    highlightCell(cellNode) {
        cellNode.getChildByName('bg').opacity = 128;
        this.highlightCellArr.push(cellNode);
    },

    unfocusCell(cellNode) {
        cellNode.getChildByName('bg').opacity = 51;
        _removeArrItem(this.highlightCellArr, cellNode);
    },

    unfocusAllCells() {
        this.highlightCellArr.map(cellNode => this.unfocusCell(cellNode));
    },

});
