import * as _GAME from '../all_modules';
export const tutRelocated = {
    init(){
        _GAME.login.addCallback(data => {
            this.isDoneTutRelocated = data.isDoneTutRelocated;
        });
    },
    checkDoneTutRelocated(){
        if(this.isDoneTutRelocated) return true;
        return false;
    },
    startTut(){
        if(_GAME.configs.off_power_item)return;
        if(this.showing) return;
        this.showing = true;
        _GAME.utilsFB.log(`ev_start_tut_relocated`,1)
        _GAME.ui.showLayer('layer_tut');
        _GAME.tutRelocated.startHandAction();
        _GAME.tutRelocated.showTextHinding();
    },
    startHandAction(){
        // console.log('vao day cha ');
        const hand = cc.find('Canvas/layer_tut/handTutorial');
        hand.stopAllActions();
        // let poiterPreviousPosition = cc.v2(cc.find('Canvas/play_area/btn_relocated').x+40,cc.find('Canvas/play_area/btn_relocated').y+40);
        // let poiterNextPosition = cc.v2(cc.find('Canvas/play_area/btn_relocated').x+60,cc.find('Canvas/play_area/btn_relocated').y+60);
        // _GAME.utils.setGlobalPositionToNode(hand,poiterPreviousPosition );
        // const nodeContainer  = cc.find('Canvas/play_area/new_item_container');
        // _GAME.utils.setGlobalPositionToNode(cc.find('Canvas/layer_tut/text_motivate'),cc.v2(nodeContainer.x + 100,nodeContainer.y+150));

        // hand.angle = 80;
        // hand.runAction(
        //     cc.repeatForever(
        //         cc.sequence(
        //             cc.moveTo(0.8,poiterNextPosition),
        //             cc.moveTo(0.8,poiterPreviousPosition)
        //         )
        //     )
        // );
        let positionShow = cc.v2(cc.find('Canvas/play_area/btn_relocated').x+10,cc.find('Canvas/play_area/btn_relocated').y);
        _GAME.utils.setGlobalPositionToNode(hand, positionShow);
        const nodeContainer  = cc.find('Canvas/play_area/new_item_container');
        _GAME.utils.setGlobalPositionToNode(cc.find('Canvas/layer_tut/text_motivate'),cc.v2(nodeContainer.x,nodeContainer.y+150));
        hand.angle = 120;
        hand.runAction(
            cc.repeatForever(
                cc.sequence(
                    cc.moveTo(1,cc.v2(hand.x,hand.y+70)),
                    cc.moveTo(1,cc.v2(hand.x,hand.y+40))
                )
            )
        );
    },
    endTut() {
        if(_GAME.configs.off_power_item)return;
        _GAME.utilsFB.log(`ev_end_tut_relocated`,1)
        // stop hand fx
        _GAME.tutRelocated.setTextToMotivate(()=>{
            _GAME.ui.hideLayer('layer_tut');

            // stop highlight target cell
            // const targetCell = cc.find(`Canvas/play_area/map/Image_HexBlock_${this.targetCell}/bg`);
            // targetCell.opacity = targetCell.orgOpacity;
            this.showing = false;
            // save to fb db that user done tut
            this.isDoneTutRelocated = true;
            _GAME.data.saveData({ isDoneTutRelocated: '1' });
        });
    },


    checkHideTutHand() {
        if (this.isDoneTutRelocated) return;
        const hand = cc.find('Canvas/layer_tut/handTutorial');
        hand.stopAllActions();
        hand.x = 9999;
    },

    checkShowTutHand() {
        if (this.isDoneTutRelocated) return;
        const hand = cc.find('Canvas/layer_tut/handTutorial');
        _GAME.tut.startHandAction();
    },
    showTextHinding(){
        let nodeLabel = cc.find('Canvas/layer_tut/hinding');
        if(nodeLabel.active == false) nodeLabel.active = true;
        _GAME.utils.setLabelText(nodeLabel,'Too hard ? Try this !');
    },
    hideTextHiding(){
        let nodeLabel = cc.find('Canvas/layer_tut/hinding');
        if(nodeLabel.active == true) nodeLabel.active = false;
    },
    setTextToMotivate(callback){
        let textMotivate = cc.find('Canvas/layer_tut/text_motivate');
        let newNodeMotivate = cc.instantiate(textMotivate);
        _GAME.utils.setLabelText(newNodeMotivate,'GOOD');
        if(newNodeMotivate.active == false)newNodeMotivate.active = true;
        newNodeMotivate.parent = textMotivate.parent;
        newNodeMotivate.runAction(
            cc.sequence(
                cc.spawn(
                    cc.scaleTo(0.5,2),
                    cc.fadeTo(0.5,255)
                ),
                cc.spawn(
                    cc.delayTime(0.6),     
                    cc.sequence(
                        cc.scaleTo(0.15,2.5),
                        cc.scaleTo(0.15,2),
                        cc.scaleTo(0.15,2.5),
                        cc.scaleTo(0.15,2),
                    )
                ),
                cc.spawn(
                    cc.moveTo(0.3,cc.v2(newNodeMotivate.x,newNodeMotivate.y+100)),
                    cc.fadeTo(0.3,0)
                ),
                cc.callFunc(()=>{
                    newNodeMotivate.destroy();
                    if(callback)callback();
                })
            )
        )
    }
}