import * as _GAME from '../all_modules';

export const utilsFB = {
    init() {
        _GAME.utilsFB.startLogSessionTime();
        if (window.FBInstant) {
            FBInstant.onPause(() => { }); // chi can goi ham de FB tick la` API da su dung
            window.fbSupportedAPIs = FBInstant.getSupportedAPIs();
            // console.table(FBInstant.getSupportedAPIs());
        }
    },


    log(eventName, quantity = 1, dataObj) {
        // _log(` logged fb-event ${eventName} `);
        // _log('Event name ==> ', eventName, " data object ==> ", dataObj);
        if (!window.FBInstant) { return; }
        FBInstant.logEvent(eventName, quantity, dataObj);
    },


    setSessionId(playerId) {
        if (window.FBInstant) {
            FBInstant.setSessionData({
                player_id: playerId
            });
        }
    },

    isSupportedAPI(name) {
        if (!window.FBInstant) return false;
        const arr = FBInstant.getSupportedAPIs();
        return arr.indexOf(name) != -1;
    },


    // log Time game play
    startLogSessionTime() {
        const logEventTimePassedTotal = (time) => {
            // console.log('logEventTimePassedTotal     ',time);
            _GAME.utilsFB.log(`ev_session_time_total`, 1, { timePassed: time });
        }
        const logEventTimePassed5 = (time)=>{
            // console.log('logEventTimePassed5     ',time);
            _GAME.utilsFB.log(`ev_session_time_5`, 1, { timePassed: time });
        }
        const logEventTimePassed10 = (time)=>{
            // console.log('logEventTimePassed10     ',time);
            _GAME.utilsFB.log(`ev_session_time_10`, 1, { timePassed: time });
        }
        const logEventTimePassed15 = (time)=>{
            // console.log('logEventTimePassed15     ',time);
            _GAME.utilsFB.log(`ev_session_time_15`, 1, { timePassed: time });
        }
        const logEventTimePassed20 = (time)=>{
            // console.log('logEventTimePassed20     ',time);
            _GAME.utilsFB.log(`ev_session_time_20`, 1, { timePassed: time });
        }
        const logEventTimePassed25 = (time)=>{
            // console.log('logEventTimePassed25     ',time);
            _GAME.utilsFB.log(`ev_session_time_25`, 1, { timePassed: time });
        }
        const logEventTimePassed30 = (time)=>{
            // console.log('logEventTimePassed30     ',time);
            _GAME.utilsFB.log(`ev_session_time_30`, 1, { timePassed: time });
        }
        logEventTimePassedTotal(0);
        logEventTimePassed5(0);
        logEventTimePassed10(0);
        logEventTimePassed15(0);
        logEventTimePassed20(0);
        logEventTimePassed25(0);
        logEventTimePassed30(0);
        let passedTime = 0;
        let passedTime5 = 0;
        let passedTime10 = 0;
        let passedTime15 = 0;
        let passedTime20 = 0;
        let passedTime25 = 0;
        let passedTime30 = 0;
        setInterval(() => {
            passedTime += 30;
            logEventTimePassedTotal(passedTime);
        }, 30000);
        setTimeout(() => {
            passedTime5 += 5;
            logEventTimePassed5(passedTime5);
        }, 5000);
        setTimeout(() => {
            passedTime10 += 10;
            logEventTimePassed10(passedTime10);
        }, 10000);
        setTimeout(() => {
            passedTime15 += 15;
            logEventTimePassed15(passedTime15);
        }, 15000);
        setTimeout(() => {
            passedTime20 += 20;
            logEventTimePassed20(passedTime20);
        }, 20000);
        setTimeout(() => {
            passedTime25 += 25;
            logEventTimePassed25(passedTime25);
        }, 25000);
        setTimeout(() => {
            passedTime30 += 30;
            logEventTimePassed30(passedTime30);
        }, 30000);
    },
}