
import * as _GAME from '../all_modules';
const LocalizeAutoTranform = require('localize_auto_tranform');
export const localize = {
    default_language :'ar_AR',
    init(){
        this.checkNodeHasLabel(); 
        this.currentLanguage = this.default_language;
        _GAME.login.addLoginDataField('CurrentLanguage');
        _GAME.login.addCallback(data=>{
            if(!data.currentLanguage){
                
                this.currentLanguage = this.default_language;
                if(data.CurrentLanguage){
                    this.currentLanguage = data.CurrentLanguage;
                }
                
                this.pointerLang = 0;
                if(_GAME.localizeConfig.totalLocalLang.indexOf(this.currentLanguage) != -1){
                    this.pointerLang = _GAME.localizeConfig.totalLocalLang.indexOf(this.currentLanguage);
                }
                this.updateCurrentLang();
            }
        });
    },
    checkNodeHasLabel(){
        this.ob ={};
        let nodeCanvas = cc.find('Canvas');
        nodeCanvas.children.map((nodeRoot)=>{
             this.searchArrayChildenNode(nodeRoot);
        });
        for (let key in this.ob) {
            let obSon = this.ob[key];
            let newObHasLabel = {};
            for (let k in obSon) {
                let element = obSon[k];
                if(this.validateNode(element)){
                    let value = element.getComponent(cc.Label).string;
                    if(parseInt(value).toString() == 'NaN'){
                        // console.log(k,'==>>>',value);
                        newObHasLabel[this.getNodePath(element)] = element;
                        element.addComponent(LocalizeAutoTranform);
                    }
                }
            }
            this.ob[key] = newObHasLabel;
        }
        // console.log('this new ob ',this.ob);
        
    },
    searchArrayChildenNode(node){
        if(node){
            let currentOb = {};
            currentOb[this.getNodePath(node)] = node;
            this.loopSearch(node.children, currentOb);
            this.ob[node.name] = currentOb;
        }
    },
    loopSearch(listChildrenClone,currentOb){
        if(listChildrenClone.length > 0){
            let lateNode = [];
            listChildrenClone.map((child)=>{
                if(!currentOb[this.getNodePath(child)]) currentOb[this.getNodePath(child)] = child;
                    
                if(child.children.length > 0 ){
                    lateNode = lateNode.concat(child.children);
                }
            });
            if(lateNode.length > 0 ){
                this.loopSearch(lateNode,currentOb);
            }else{
                // console.log('=================================');
                return currentOb;
            }
        }else{
            // console.log('=================================');
            return currentOb;
        }
        
    },
    
    validateNode(node){
        if(node.getComponent(cc.Label)) return true;
        return false;
    },
    getNodePath(node){
        let path = '';
        if(node){
            let string = node.name;
            let parentnode = node.parent;
            path = parentnode.name+'/'+string;
            while(parentnode.name != 'Canvas'){
                let lastPath = path;
                parentnode = parentnode.parent;
                path = parentnode.name+'/'+lastPath;
                if(parentnode.name == 'Canvas'){
                    break;
                }
            }
        }
        return path;
    },
    convertPosition(cacheValue){
        let result = '';
        if(cacheValue.length != 0){
            if(_GAME.localizeConfig.localize){
                if(this.currentLanguage){
                    if(_GAME.localizeConfig.localize[cacheValue]){
                        result = _GAME.localizeConfig.localize[cacheValue][this.currentLanguage];
                        // console.log(cacheValue,' =====>>>>>> ',result);    
                    }else{
                        if(cacheValue == 'ENGLISH'){
                            result = _GAME.localizeConfig.meaningLocalLang[this.pointerLang];
                        }
                    }
                }
            }
        }
        return result;
    },
    onClickChangeLocalize(){
        this.pointerLang++;
        if(this.pointerLang >=_GAME.localizeConfig.totalLocalLang.length) this.pointerLang = 0;
        this.currentLanguage =_GAME.localizeConfig.totalLocalLang[this.pointerLang];
        _GAME.data.saveData({'CurrentLanguage': this.currentLanguage});
        this.emitProxyNode();
    },
    emitProxyNode(){
        const canvas = cc.find ('Canvas');
        // console.log(canvas);
        canvas.emit('TRANFORM_TEXT');
        
    },
    getProxyNode(){
        return cc.find('Canvas');
    },
    updateCurrentLang(){
        this.emitProxyNode();
    }
}
