import * as _GAME from '../all_modules';
const BlockAsset = require('blockAsset');
export const fx = {
    init() {
        _GAME.fx.bubbleForever(cc.find('Canvas/layer_leaderboard/header/btn_brag'));
        // this.shiningStar();
    },

    bubbleForever(node, timespan = 0.5, delay = 0, saleFactor1 = 1, scaleFactor2 = 1.2) {
        const orgScale = node.orgScale || node.scale;
        if (!node.orgScale) { node.orgScale = orgScale; }
        node.stopAllActions();
        node.runAction(cc.repeatForever(cc.sequence(
            cc.delayTime(delay),
            cc.scaleTo(timespan, orgScale * scaleFactor2),
            cc.scaleTo(timespan, orgScale * saleFactor1),
            cc.scaleTo(timespan, orgScale * scaleFactor2),
            cc.scaleTo(timespan, orgScale * saleFactor1),
        )));
    },
    bubbleForeverLabel(node, timespan = 0.5, delay = 0, saleFactor1 = 1, scaleFactor2 = 1.1) {
        const orgScale = node.orgScale || node.scale;
        if (!node.orgScale) { node.orgScale = orgScale; }
        node.stopAllActions();
        node.runAction(cc.repeatForever(cc.sequence(
            cc.delayTime(delay),
            cc.scaleTo(timespan, orgScale * scaleFactor2),
            cc.scaleTo(timespan, orgScale * saleFactor1),
            cc.scaleTo(timespan, orgScale * scaleFactor2),
            cc.scaleTo(timespan, orgScale * saleFactor1),
        )));
    },
    bubble(node, callback, timespan = 0.5, delay = 0, saleFactor1 = 1, scaleFactor2 = 1.2) {
        const orgScale = node.orgScale || node.scale;
        if (!node.orgScale) { node.orgScale = orgScale; }
        node.stopAllActions();
        node.runAction(cc.sequence(
            cc.delayTime(delay),
            cc.scaleTo(timespan, orgScale * scaleFactor2),
            cc.scaleTo(timespan, orgScale * saleFactor1),
            cc.scaleTo(timespan, orgScale * scaleFactor2),
            cc.scaleTo(timespan, orgScale * saleFactor1),
            cc.callFunc(() => {
                if (callback) callback();
            })
        ));
    },

    fxScoreNormal(keyCellNode, scoredCellArr, callback, fxTime = 0.3) {
        const globalPos = _GAME.utils.getGlobalPosition(keyCellNode);
        scoredCellArr.map((linkedCellNumber, index) => {
            // run action to make cell flies to keyCell
            const orgCellNode = cc.find(`Canvas/play_area/map/Image_HexBlock_${linkedCellNumber}`);
            const fxContainer = cc.find(`Canvas/play_area/fx_container`);
            const cellNode = _GAME.utils.copyNodeTo(orgCellNode, fxContainer);
            const posDiff = _GAME.utils.global2LocalPosDiff(cellNode, globalPos.x, globalPos.y);
            cellNode.getChildByName('bg').active = false;
            cellNode.runAction(cc.sequence(
                cc.moveBy(fxTime, posDiff).easing(cc.easeIn(2)),
                cc.callFunc(() => {
                    cellNode.removeFromParent(true);
                    // only call once when 3 cells flew to center cell
                    if (!index && callback) callback();
                })
            ));
        });
    },


    fxScore7(keyCellNode, orgScoredCellArr, callback, fxTime = 0.3) {
        _GAME.audio.playSound('catch_seven');
        const fxContainer = cc.find(`Canvas/play_area/fx_container`);
        let keyFxNode;
        const scoredCellNodeArr = orgScoredCellArr.map(sCellNum => {
            const orgCellNode = cc.find(`Canvas/play_area/map/Image_HexBlock_${sCellNum}`);
            const cellNode = _GAME.utils.copyNodeTo(orgCellNode, fxContainer);
            cellNode.getChildByName('bg').active = false;
            if (orgCellNode == keyCellNode) keyFxNode = cellNode;
            return cellNode;
        });

        const fxForSingleCell = (cellNode, index) => {
            cellNode.runAction(cc.sequence(
                cc.scaleTo(fxTime, 1.2),
                cc.scaleTo(fxTime, 1),
            ));
            cellNode.runAction(cc.sequence(
                cc.delayTime(fxTime),
                cc.fadeOut(fxTime),
                cc.callFunc(() => {
                    cellNode.removeFromParent(true);
                    if (!index && callback) callback();
                }),
            ));

            const blockNode = cellNode.getChildByName('sample_block');
            blockNode.getChildByName('Image_HexBlock').runAction(cc.tintTo(fxTime, 255, 255, 255))
            // blockNode.getChildByName('Image_HexBlock_Shadow').runAction(cc.tintTo(fxTime, 255, 255, 255))
            // blockNode.getChildByName('block7stroke').runAction(cc.fadeOut(fxTime));
            blockNode.getChildByName('label').runAction(cc.fadeOut(fxTime));
        };
        fxForSingleCell(keyFxNode, 1);

        _GAME.utils.setTimeout(() => {
            keyFxNode.stopAllActions();
            keyFxNode.getChildByName('sample_block').children.map(childNode => childNode.stopAllActions());

            scoredCellNodeArr.map((cellNode, index) => fxForSingleCell(cellNode, index))
        }, parseInt(fxTime * 1000 / 2))
    },




    // // show tooltip
    freeToolTipNodeArr: [],
    showScoreToolTip(text, targetNode, extraY = 60, callback, rangeY = 100, fxTime = 1.6, fadeInTime = 0.2, delayTime = 0) {
        const sampleNode = cc.find('Canvas/play_area/fx_container/sample_score_tool_tip');
        const labelNode = this.freeToolTipNodeArr.pop() || _GAME.utils.copyNodeTo(sampleNode, cc.find('Canvas/play_area/fx_container'));
        _GAME.utils.setLabelText(labelNode, text)
        _GAME.utils.setGlobalPositionToNode(labelNode, targetNode);
        labelNode.y += extraY;

        labelNode.active = true;
        labelNode.zIndex = 999;
        labelNode.opacity = 0;
        labelNode.runAction(cc.sequence(
            cc.fadeIn(fadeInTime),
            cc.delayTime(delayTime),
            cc.spawn(
                cc.moveBy(fxTime, cc.v2(0, rangeY)),
                cc.fadeOut(fxTime),
            ),
            cc.callFunc(() => {
                labelNode.active = false;
                this.freeToolTipNodeArr.push(labelNode);
                if (callback) callback();
            })
        ))
    },

    freeFlyScoreNodeArr: [],
    singleScoreLabelFly(sourceNode, scoreText, fxTime = 0.3) {
        const sampleNode = cc.find('Canvas/play_area/fx_container/sample_label_fly_score');
        const labelNode = this.freeFlyScoreNodeArr.pop() || _GAME.utils.copyNodeTo(sampleNode, cc.find('Canvas/play_area/fx_container'));
        _GAME.utils.setLabelText(labelNode, scoreText);
        _GAME.utils.setGlobalPositionToNode(labelNode, sourceNode);

        const targetNode = cc.find('Canvas/play_area/labels/label_score');
        const targetGPos = _GAME.utils.getGlobalPosition(targetNode);

        labelNode.active = true;
        labelNode.zIndex = 999;
        const posDiff = _GAME.utils.global2LocalPosDiff(labelNode, targetGPos.x, targetGPos.y);
        labelNode.runAction(cc.sequence(
            cc.moveBy(fxTime, posDiff).easing(cc.easeIn(1.4)),
            cc.callFunc(() => {
                labelNode.active = false;
                this.freeFlyScoreNodeArr.push(labelNode);
            })
        ))
    },

    multiScoreLabelFly7(scoredCellArr, totalScore, callback) {
        const timeStep = 0.07;
        const scoreText = '+' + _GAME.utils.localizeNumber(parseInt(totalScore / scoredCellArr.length));
        scoredCellArr.map((sCellNum, index) => {
            const cellNode = cc.find(`Canvas/play_area/map/Image_HexBlock_${sCellNum}`);
            _GAME.utils.setTimeout(() => {
                this.singleScoreLabelFly(cellNode, scoreText)
            }, timeStep * 1000 * index);
        });
        _GAME.utils.setTimeout(() => callback && callback(), timeStep * scoredCellArr.length * 1000);
    },

    multiScoreLabelFlyNormal(keyCellNode, scoredCellArr, totalScore, callback) {
        const timeStep = 0.07;
        const scoreText = '+' + _GAME.utils.localizeNumber(parseInt(totalScore / scoredCellArr.length));

        scoredCellArr.map((sCellNum, index) => {
            _GAME.utils.setTimeout(() => {
                this.singleScoreLabelFly(keyCellNode, scoreText)
            }, timeStep * 1000 * index);
        });
        _GAME.utils.setTimeout(() => callback && callback(), timeStep * scoredCellArr.length * 1000);
    },

    shiningStar() {
        const nodeStar = cc.find('Canvas/bg/Image_Background_Stars');
        nodeStar.stopAllActions();
        nodeStar.runAction(
            cc.sequence(
                cc.fadeTo(1.5, 63.5),
                cc.fadeTo(3, 255),
                cc.fadeTo(1.5, 127.5),
            ).repeatForever()
        )
    },
    showTextGreate(node){
        if(node){
            node.scale = 1;
            node.runAction(
                cc.scaleTo(0.3,1)
            )
        }
    },
    animStar(arrayNode){
        if(arrayNode && arrayNode.length != 0){
            let timeDelay = 0;
            arrayNode.map((item)=>{
                item.lastScale = item.scale;
                item.scale = 0;
                item.runAction(
                    cc.sequence(
                        cc.delayTime(timeDelay),
                        cc.spawn(
                            cc.rotateTo(0.5,-360),
                            cc.scaleTo(0.5,item.lastScale)
                        ),
                    )
                );
                timeDelay+=1;
            });
        }
    },
    effectForInfo(target, posBonus) {
        target.opacity = 0;
        target.x += posBonus;
        let posMove = target.x - posBonus;
        cc.tween(target)
            .delay(0.5)
            .parallel(
                cc.tween().to(1, { opacity: 255 }),
                cc.tween().to(0.5, { position: cc.v2(posMove, target.y) })
            )
            .start();
    },
    effectForButton(target, timeDelay) {
        target.active = false;
        cc.tween(target)
            .delay(timeDelay)
            .call((node) => {
                node.active = true;
            })
            .to(0.2, { scaleX: 1.3, scaleY: 1.3 }, { easing: "backOut" })
            .to(0.2, { scaleX: 1, scaleY: 1 })
            .start();
    },
    effectBoom(node,callback){
        if(node){
            cc.tween(node)
                .call(()=>{
                    let anim = node.getChildByName('sample_block').getChildByName('anim');
                    if(anim){
                        if(anim.active == false)anim.active = true;
                        let component = anim.getComponent(cc.Animation);
                        component.play(component._clips[0].name);
                        component.on('lastframe', ()=>{});
                        component.on('finished',()=>{
                            // let bg = node.getChildByName('sample_block').getChildByName('Image_HexBlock');
                            // let number = node.getChildByName('sample_block').getChildByName('label');
                            // let special = node.getChildByName('sample_block').getChildByName('x');
                            // if(special.active == true)special.active = false;
                            // bg.active = false;
                            // number.active = false;
                            // anim.active = false;
                            if(callback)callback();
                        });
                    }
                })
                .to(0.2,{opacity : 0})
                .start();
            
        }
    },
    effectTriple(isActive,callback){
        const lableTripple = cc.find('Canvas/play_area/triple');
        if(isActive){
            if(lableTripple.active == false) lableTripple.active =true;
            // lableTripple.color = new cc.Color().fromHEX('#33FF00');
            lableTripple.stopAllActions();
            _GAME.fx.bubbleForeverLabel(lableTripple);
            if(callback) callback();
            _GAME.fx.fxBtnTriple(isActive);
        }else{
            // lableTripple.color = new cc.Color().fromHEX('#ffffff');
            lableTripple.stopAllActions();
            if(lableTripple.active == true)lableTripple.active =false;
            lableTripple.scale = 1;
            _GAME.fx.fxBtnTriple(isActive);
        }
        
    },
    fxBtnTriple(isActive){
        const btnTriple = cc.find('Canvas/play_area/btn_triple');
        let color = (isActive)?'#525252':'#ffffff';
        if(btnTriple){
            btnTriple.color = new cc.Color().fromHEX(color);

            let icon = btnTriple.getChildByName('icon');
            icon.color = new cc.Color().fromHEX(color);

            let label = btnTriple.getChildByName('bg_total_triple').getChildByName('total');
            label.color = new cc.Color().fromHEX(color);
            
            let iconVideo = btnTriple.getChildByName('bg_total_triple').getChildByName('video');
            iconVideo.color = new cc.Color().fromHEX(color);
            if(!_GAME.game.triplePoint){
                if(_GAME.uiPowerItem.totalTriple <= 0){
                    label.active =false;
                    iconVideo.active =true;
                }else{
                    label.active =true;
                    iconVideo.active =false;
                }
               
                btnTriple.getChildByName('bg_total_triple').active = true;
            }
            
        }
    },
    showAnimRelocated(arrayAnim,arrayMainNode,callback){
        if(arrayAnim.length != 0){
            arrayAnim.map((node,index)=>{
                let mainBlockNumber = _GAME.utils.randomArrItem(_GAME.configs.blockNumber[1]);
                // _GAME.utils.randomArrItem([3,5]);
                //set node number
                let mainNode = arrayMainNode[index];
                let sampleBlock = mainNode.getChildByName('sample_block');
                _GAME.mapVisual.decorBlock(sampleBlock,mainBlockNumber);
                _GAME.map.currentState[mainNode.cellNumber] = mainBlockNumber;
                
                //set anim number
                let arrayNumber = [];
                for (let index = 0; index < 3; index++) {arrayNumber.push(_GAME.utils.randomArrItem(_GAME.configs.blockNumber[1]))}
                arrayNumber.push(mainBlockNumber);
                // console.log('node', index ,'/',arrayNumber);
                //reset child
                let sample_block = node.getChildByName('sample_block');
                sample_block.children.map((child)=>{child.active = false;})
                let nodeMark = sample_block.getChildByName('anim_relocated');
                if(nodeMark.active == false) nodeMark.active = true;

                let nodeImageAnim = sample_block.getChildByName('anim_relocated').getChildByName('Image_Anim');
                let nodeLabel = sample_block.getChildByName('anim_relocated').getChildByName('label');
                _GAME.fx.rollingItem(nodeLabel,nodeImageAnim,arrayNumber,()=>{
                    if(callback) callback();
                });
            });
        }
    },
    rollingItem(nodeLabel,nodeImageAnim,arrayNumber,callback){
        let centerPoint = cc.v2(0,2);
        let topPoint = cc.v2(0,80);
        let botPoint = cc.v2(0,-80);
        nodeLabel.position = topPoint;
        nodeLabel.opacity = 0;
        let pointShow = arrayNumber.shift();
        cc.tween(nodeLabel)
            .parallel(
                cc.tween(nodeLabel)
                    .call(()=>{
                        // nodeLabel.getComponent(cc.Label).string = pointShow;
                        _GAME.utils.setLabelText(nodeLabel,pointShow);
                    })
                    .parallel(
                        cc.tween(nodeLabel).to(0.15,{position:centerPoint}).start(),
                        cc.tween(nodeLabel).to(0.15,{opacity:255}).start(),
                    )
                    .start(),
                cc.tween(nodeImageAnim)
                    .call(()=>{nodeImageAnim.getComponent(cc.Sprite).spriteFrame = nodeImageAnim.getComponent(BlockAsset).assetBlock[parseInt(pointShow)-1] })
                    .start()
            )
            .delay(0.15)
            .call(()=>{
                if(arrayNumber.length != 0){
                    cc.tween(nodeLabel)
                        .parallel(
                            cc.tween(nodeLabel).to(0.15,{position:botPoint}).start(),
                            cc.tween(nodeLabel).to(0.15,{opacity:0}).start(),
                        )
                        .call(()=>{
                            _GAME.fx.rollingItem(nodeLabel,nodeImageAnim,arrayNumber,callback);
                        })
                        .start();
                }else{
                    if(callback)callback();
                }
            })
            .start();

    },
    nodeMoveAndHidden(clone,callback){
        if(clone){
            cc.tween(clone)
                .parallel(
                    cc.tween(clone).to(1,{position:cc.v2(0,140)}),
                    cc.tween(clone).to(1,{opacity:200})
                )
                .delay(0.5)
                .to(0.2,{opacity:0})
                .call(
                    ()=>{
                        if(callback)callback();
                        clone.destroy();
                    }
                )
                .start()
        }
    }
};