import * as _GAME from '../all_modules';

export const data = {
    init: () => {
        _GAME.data.isUserReady = true;
    },

    // ==============================================================================
    //  SAVE/LOAD DATA TO/FROM FACEBOOK (LOCAL-STORAGE)

    // save/ load data =========================================================
    saveData(dataObj, callbackSuccess, calbackFail) {
        if (window.FBInstant) {
            FBInstant.player.setDataAsync(dataObj).then(() => {
                // _log(' _GAME.pseudoAPI.saveData :: dataObj = ', dataObj);
                if (callbackSuccess) { callbackSuccess(); }
            });

        } else {
            _GAME.utils.setTimeout(() => {
                for (let key in dataObj) {
                    try {
                        localStorage.setItem(key, JSON.stringify(dataObj[key]));
                    } catch (e) { _log(e); }
                }
                if (callbackSuccess) { callbackSuccess(); }
            }, 500);
        }
    },

    loadData(keyArr, callbackSuccess) {
        if (window.FBInstant) {
            FBInstant.player.getDataAsync(keyArr).then((data) => {
                // _log(' _GAME.pseudoAPI.loadData :: data = ', data);
                if (callbackSuccess) {
                    callbackSuccess(data);
                }
            });

        } else {
            _GAME.utils.setTimeout(() => {
                const data = {};
                keyArr.map((key) => {
                    const strData = localStorage.getItem(key);
                    if (strData) {
                        try {
                            data[key] = JSON.parse(strData);
                        } catch (e) {
                            if (key == 'Field') {

                            }
                            // _log(e);
                            // _log(`strData = ${strData} // key=${key}`);
                        }
                    }
                });
                if (callbackSuccess) { callbackSuccess(data); }
            }, 500);
        }
    },


    loadEntryPointData() {
        if (window.FBInstant) {
            const data = FBInstant.getEntryPointData();
            if (data && data.player_sent_id) {
                _GAME.user.playerIdShare = data.player_sent_id;
            }
            _GAME.user.entryPointData = data;
            // _GAME.user.entryPointData = {
            //     clanId: "540f598f-f5f0-49d9-a200-0e660534f069",
            //     playerInviteId: "07540897-c785-45eb-8be9-bac87874be13"
            // };
            // _log("ENTRY POINT DATA", data);
        }
    }

}