import * as _GAME from '../all_modules';
export const map = {
    cellLinks: require('cell_links'),
    cellLinkMap: {},
    currentState: null,
    selectedCell: null,
    minorCell: null,

    init() {
        // parse the cellLinks to a version that can check each single cell => easier to user & save performance at runtime
        for (let linkName in this.cellLinks) {
            const cellArr = linkName.split('_');

            const cellNode0 = cc.find(`Canvas/play_area/map/Image_HexBlock_${cellArr[0]}`);
            const cellNode1 = cc.find(`Canvas/play_area/map/Image_HexBlock_${cellArr[1]}`);
            const isCell0Dominated = cellNode0.x > cellNode1.x;

            this.cellLinkMap[cellArr[0]] = this.cellLinkMap[cellArr[0]] || {};
            this.cellLinkMap[cellArr[0]][cellArr[1]] = this.cellLinks[linkName] + (isCell0Dominated ? '_dominated' : '');

            this.cellLinkMap[cellArr[1]] = this.cellLinkMap[cellArr[1]] || {};
            this.cellLinkMap[cellArr[1]][cellArr[0]] = this.cellLinks[linkName] + (!isCell0Dominated ? '_dominated' : '');
        }
    },

    resetMap() {
        this.currentState = {}
        _GAME.mapVisual.resetMap();
    },

    setSingleCell(cellNumber, blockNumber) {
        this.currentState[cellNumber] = blockNumber;
        _GAME.mapVisual.renderSingleCell(cellNumber, blockNumber);
    },

    clearSingleCell(cellNumber) {
        this.currentState[cellNumber] = null;
        _GAME.mapVisual.resetSingleCell(cellNumber);
    },

    setCellGroup(cellInfoArr) {
        cellInfoArr.map(cellInfo => {
            this.setSingleCell(cellInfo.cellNumber, cellInfo.blockNumber);
        })
    },

    clearCellGroup(cellInfoArr) {
        cellInfoArr.map(cellNumber => {
            this.clearSingleCell(cellNumber);
        })
    },


    // ----------------------

    checkNewItemDroppableAtCell(cellNumber) {
        // check if cell is still free
        if (this.currentState[cellNumber]) return;

        // get current bias type to get minorCell
        const biasType = _GAME.game.currentItemBiasType;
        // _log(` biasType = ${biasType} `);
        if (biasType == 'single_block') return true;

        const biasLinks = this.cellLinkMap[cellNumber];
        let minorCellNumber;
        for (let otherCellNumber in biasLinks) {
            if (biasLinks[otherCellNumber] == biasType) {
                minorCellNumber = otherCellNumber;
                break;
            }
        }
        if (!minorCellNumber) return;
        if (this.currentState[minorCellNumber]) return;
        // _log(` checkNewItemDroppableAtCell >>  cellNumber = ${cellNumber} // minorCellNumber=${minorCellNumber} // this.currentState=`, this.currentState);

        return minorCellNumber;
    },


    // ----------------------
    dropNewItemAtCell(keyCellNumber, minorCellNumber) {
        this.setSingleCell(keyCellNumber, _GAME.game.currentKeyBlockNumber);
        if (minorCellNumber) this.setSingleCell(minorCellNumber, _GAME.game.currentMinorBlockNumber);
    },

    // check available blockNumber of new item
    checkAvailableNewItemInfo() {
        let blockCount = 1;
        const biasTypeSet = new Set;
        // _log(` checkAvailableNewItemInfo >>>>> this.currentState =  `, this.currentState);
        for (let cellNumber in this.cellLinkMap) {
            if (this.currentState[cellNumber + '']) continue;
            const linkedCells = this.cellLinkMap[cellNumber];
            for (let linkedCellNumber in linkedCells) {
                if (this.currentState[linkedCellNumber + '']) continue;
                blockCount = 2;
                const biasTypeName = linkedCells[linkedCellNumber].replace('_dominated', '');
                // if (!biasTypeSet.has(biasTypeName)) _log(`checkAvailableNewItemInfo >> biasTypeName=${biasTypeName} // added by cellNumber(${cellNumber}) linked to ${linkedCellNumber}// orgLinkName = ${linkedCells[linkedCellNumber]} `);
                biasTypeSet.add(biasTypeName);
            }
        }

        return { blockCount, biasTypeArr: Array.from(biasTypeSet) };
    },

    isFulled() {
        // return Object.values(this.currentState).filter(item => !!item).length == 37;   // for map 4x4
        return Object.values(this.currentState).filter(item => !!item).length == 19;      // for map 3x3
    },


    // checkBlockScore()
    checkBlockScore(cellNumber) {

        // get array of cells that linked 
        const blockNumber = this.currentState[cellNumber];
        const checkedCellNumber = { [cellNumber]: true };
        const scoredCellArr = [cellNumber];
        // _log(`map >> ------------- checkBlockScore // cellNumber=${cellNumber} // blockNumber=${blockNumber} `);

        // let safeCount = 37; // for map 4x4
        let safeCount = 19;  // for map 3x3
        const cellLinkMap = this.cellLinkMap;
        const recursiveFunc = (cellNumber) => {
            if (--safeCount <= 0) return;
            const expandingCellArr = [];
            // _log(` this.cellLinkMap =  `, cellLinkMap);
            for (let linkedCellNumber in cellLinkMap[cellNumber]) {
                // _log(` checkedCellNumber[${linkedCellNumber}] = ${checkedCellNumber[linkedCellNumber]} `);
                if (checkedCellNumber[linkedCellNumber]) continue;
                checkedCellNumber[linkedCellNumber] = true;

                // _log(`checking linkedCellNumber(${linkedCellNumber}).block = ${this.currentState[linkedCellNumber]}`);
                if (this.currentState[linkedCellNumber] != blockNumber) continue;
                scoredCellArr.push(linkedCellNumber);
                expandingCellArr.push(linkedCellNumber);
            }
            expandingCellArr.map(cellNumber => recursiveFunc(cellNumber));
        }
        recursiveFunc(cellNumber);
        // _log(`map >> checkBlockScore // safeCount=${safeCount} // scoredCellArr=${scoredCellArr} // checkedCellNumber=${Object.keys(checkedCellNumber)} `);

        // check block scores
        const minLinkedCellToScore = 3;
        if (scoredCellArr.length < minLinkedCellToScore) return false;
        return scoredCellArr;
    },
}