import * as _GAME from '../all_modules';

export const promo = {
    countScale: 0,
    init() {
        // adjust leaderboard UI due to configs.isShowPromoGames
        cc.find('Canvas/layer_leaderboard/content_bg/middle_container/promo_panel').active = window.canPromo; //_GAME.configs.isShowPromoGames;
        cc.find('Canvas/layer_leaderboard/content_bg/middle_container/leaderboard/leaderboard_content').height = window.canPromo ? 375 : 573; //_GAME.configs.isShowPromoGames ? 375 : 573;
    },
    renderButtonByIcon() {
        const promoPanel = cc.find('Canvas/layer_leaderboard/content_bg/middle_container/promo_panel');
        if (promoPanel) {
            if (_GAME.configs.listIdPromoGame) {
                promoPanel.children.map((item, index) => {
                    _GAME.utils.addButtonComp(item, () => {
                        _GAME.utilsFB.log('ev_click_button_promo_game', 1, { Position: index });
                        _GAME.social.switchPromoGame(_GAME.configs.listIdPromoGame[index]);
                    });

                })
            }
        }


        // this.countScale = 0;
        // this.actionIconPromo();
    },
    actionIconPromo() {
        const promoPanel = cc.find('Canvas/layer_leaderboard/content_bg/middle_container/promo_panel');
        const itemScale = promoPanel.children[this.countScale];
        if (itemScale) {
            _GAME.fx.bubble(itemScale, () => {
                _GAME.utils.setTimeout(() => {
                    this.countScale++;
                    if (this.countScale > 2) this.countScale = 0;
                    this.actionIconPromo();
                }, 500);
            });
        }
    }
}