import * as _GAME from '../all_modules'

export const friend ={
    listFrienlds: [],
    cacheFriend: [],
    init(){
        _GAME.utils.setTimeout(()=>{
        });
       
    },
    loadDataFrienld() {
        if (window.FBInstant) {
            FBInstant.player.getConnectedPlayersAsync()
                .then(function(players) {
                    _GAME.friend.listFrienlds = [];
                    players.map(function(player) {
                        _GAME.friend.listFrienlds.push({ id: player.getID(), score: 0, name: player.getName(), photo: player.getPhoto() });
                    });
                    // console.log('_GAME.friend.listFrienlds  ',_GAME.friend.listFrienlds);
                });
        }
    },
    getRandomFriendInList() {
        let result = null;
        if (_GAME.friend.listFrienlds && _GAME.friend.listFrienlds.length != 0) {
            let friendRandom = _GAME.utils.randomArrItem(_GAME.friend.listFrienlds);
            // console.log('friendRandom   ', friendRandom);
            if (friendRandom) {
                if (_GAME.friend.cacheFriend.length == 0) {
                    result = friendRandom;
                    _GAME.friend.cacheFriend.push(friendRandom);
                } else {
                    // console.log('_GAME.friend.cacheFriend  ', _GAME.friend.cacheFriend);
                    let isPassFrienld = _GAME.friend.cacheFriend.find(user => user.id == friendRandom.id);
                    if (!isPassFrienld) {
                        // console.log('Vao day nhan tiep af');
                        result = friendRandom;
                        _GAME.friend.cacheFriend.push(friendRandom);
                    } else {
                        let total = 10;
                        while (isPassFrienld) {
                            friendRandom = _GAME.utils.randomArrItem(_GAME.friend.listFrienlds);
                            isPassFrienld = _GAME.friend.cacheFriend.find(user => user.id == friendRandom.id);
                            total--;
                            if (!isPassFrienld || total == 0) {
                                // console.log('friendRandom  while ', friendRandom);
                                result == isPassFrienld;
                                return result;
                                break;
                            }
                        }
                    }
                }
            }
        }
        return result;
    },
    validatePanelCanShow(){
        if(_GAME.friend.cacheFriend.length == _GAME.friend.listFrienlds.length) return false;
        if(_GAME.configs.maxCountCancelChallenge <= _GAME.uiChallenge.countCancelChallenge)return false;
        return true;      
    }
        
}