    async initBase64Picture(target: cc.Node) {
        if (!target.activeInHierarchy) {
            target.parent.active = true;
            target.parent.parent.active = true;
        }

        // _log(target);
        const cameraNode = new cc.Node();
        target.addChild(cameraNode);
        cameraNode.position = target.position;
        let camera = cameraNode.addComponent(cc.Camera);
        camera.cullingMask = 0xffffffff;
        let texture = new cc.RenderTexture();
        texture.initWithSize(target.width, target.height);
        camera.targetTexture = texture;
        camera.zoomRatio = 3.27;
        // camera.zoomRatio = 1;

        let width = texture.width;
        let height = texture.height;
        let _canvas = document.createElement('canvas');
        _canvas.width = width;
        _canvas.height = height;

        let ctx = _canvas.getContext('2d');
        camera.render(target);
        let data = texture.readPixels();
        // write the render data

        let rowBytes = width * 4;
        for (let row = 0; row < height; row++) {
            let srow = height - 1 - row;
            let data2 = new Uint8ClampedArray(data.buffer, srow * width * 4, rowBytes);
            let imageData = new ImageData(data2, width, 1);
            ctx.putImageData(imageData, 0, row);
        }
        var dataURL = _canvas.toDataURL();

        setTimeout(() => target.parent.active = false, 2000);
        return dataURL;
    }

    async initPayload(target: cc.Node, content: string, ctaText: string, extraData?: any) {
        try {
            const base64Image = await this.initBase64Picture(target);
            const payload: FBInstant.CustomUpdatePayload = {
                action: 'CUSTOM',
                // text: _GAME.language.generateMsgTextObject(content),
                text: content,
                // cta: _GAME.language.generateMsgTextObject(ctaText),
                cta: ctaText,
                image: base64Image,
                template: 'play_turn',
                strategy: 'IMMEDIATE',
                data: extraData,
                notification: 'PUSH',
            };
            return payload;
        } catch (error) {
            throw error;
        }
    }
