window._NO_CONSOLE_LOG = true;
// window._NO_DEVICELOG = true;

// ############## SOUND VOLUME
window._soundVolume = 1; // value range = 0 -> 1

// ############## COMMON GLOBAL VARS ###########################################
//=== device info
window._isANDROID = (cc.sys.os == cc.sys.OS_ANDROID);
window._isIOS = (cc.sys.os == cc.sys.OS_IOS);

//--- global logics
window._max = Math.max;
window._min = Math.min;
window._round = Math.round;
window._floor = Math.floor;
window._ceil = Math.ceil;
window._abs = Math.abs;
window._random = Math.random;
window._sqrt = Math.sqrt;
window._atan2 = Math.atan2;
window._PI = Math.PI;
window._removeArrItem = (Arr, item) => {
    const index = Arr.indexOf(item);
    if (index == -1) { return; }
    Arr.splice(index, 1);
    return true;
}

window._time = () => (new Date()).getTime(); //-- get current time in milliseconds
window._isString = (x) => { return typeof x === 'string'; };
window._isFunction = (functionToCheck) => { return functionToCheck && {}.toString.call(functionToCheck) === '[object Function]'; }
window._log = (...args) => {
    // return;
    if (!window._NO_CONSOLE_LOG) { //---- for debug
        try {
            console.log(...args);
        } catch (e) {}
    }
};